    description:
      '\n<p>Western Province Cricket Association (WPCA) with its headquarters situated at Newlands Cricket Ground, controls all Amateur Cricket in the Province and subscribes to the principles of the Employment Equity Act hereby invites candidates to apply for this vacancy.</p>\n\n\n\n<p>PURPOSE OF THE ROLE</p>\n\n\n\n<p>To incumbent will be responsible for the following key delivery areas:</p>\n\n\n\n<p>&nbsp;Effective execution of the KFC Mini Cricket Programme within the Province.</p>\n\n\n\n<p>The role reports to the Cricket Services Manager.</p>\n\n\n\n<p>KEY OUTPUT NO 1</p>\n\n\n\n<p>To administer and execute a sustainable KFC Mini Cricket programme.</p>\n\n\n\n<p>&nbsp;Successful implementation of Zonal administrative structures to assist in the coordination of the programme.</p>\n\n\n\n<p>&nbsp;Coordination of the Annual Provincial Festival and Seminars.</p>\n\n\n\n<p>&nbsp;Nominating delegates in conjunction with the Provincial Committee for National Seminars.</p>\n\n\n\n<p>&nbsp;Compiling and delivery of activities on the Annual Planner for the programme  courses, refresher courses and forums, meetings, festivals and seminars.</p>\n\n\n\n<p>&nbsp;Assess and ensure that the standard of KFC Mini-Cricket is maintained in all regions.</p>\n\n\n\n<p>&nbsp;Maintaining sufficient level of stock by initiating and finalising all clothing and playing equipment orders.</p>\n\n\n\n<p>&nbsp;Coordinate the distribution of clothing and playing equipment to the zonal structures.</p>\n\n\n\n<p>&nbsp;Perform administrative duties including drafting of fixtures.</p>\n\n\n\n<p>&nbsp;Liaise with sponsors / CSA and assist in the implementation of new strategies.</p>\n\n\n\n<p>&nbsp;Liaise with the KFC Local Store Marketers in seeking support and effective rollout of the programmes at Provincial and Zonal levels.</p>\n\n\n\n<p>&nbsp;Appoint accredited coaches to present courses in the Province.</p>\n\n\n\n<p></p>\n\n\n\n<p>KEY OUTPUT NO 2</p>\n\n\n\n<p>Coordinate Logistics and Travel.</p>\n\n\n\n<p>&nbsp;Co-ordinate Travel and Accommodation arrangements for the National Seminar.</p>\n\n\n\n<p>&nbsp;Arrange venue for Provincial meetings.</p>\n\n\n\n<p>&nbsp;Monitor and support Zones in executing the Annual Planner.</p>\n\n\n\n<p>&nbsp;Ensuring scheduled fixtures take place within adequate and safe surroundings.</p>\n\n\n\n<p>&nbsp;Ensure that all Provincial Committee engagements are fulfilled, including meetings, drafting of policies, minute taking etc.</p>\n\n\n\n<p>KEY OUTPUT NO 3</p>\n\n\n\n<p>Data Capturing and Compiling of Reports</p>\n\n\n\n<p>&nbsp;Maintain the database of registered / participating schools, coaches, players and regional</p>\n\n\n\n<p>committees.</p>\n\n\n\n<p>&nbsp;Compile and submit status reports on fixtures, matches completed, new schools added,</p>\n\n\n\n<p>schools no longer participating, initiatives to drive good behaviour and sustainability of the programme.</p>\n\n\n\n<p>&nbsp;Compiling accurate data and information for the EPG report on programmes.</p>\n\n\n\n<p>KEY QUALIFICATIONS AND REQUIREMENTS</p>\n\n\n\n<p>&nbsp;Knowledge of cricketing fraternity/business (Associate and CSA programmes set up and requirements).</p>\n\n\n\n<p>&nbsp;Sound Computer literacy skills with experience in the use of database programmes and stats.</p>\n\n\n\n<p>&nbsp;Affilate/Associate and CSA Transformation requirements.</p>\n\n\n\n<p>&nbsp;A strong commitment to advancing transformation in cricket.</p>\n\n\n\n<p>&nbsp;Ability to be service orientated  achieving results and stakeholder satisfaction.</p>\n\n\n\n<p>&nbsp;Grade 12 qualification, while a Sports Management Diploma or Degree would be an advantage.</p>\n\n\n\n<p>&nbsp;CSA Level I Coaching Qualification.</p>\n\n\n\n<p>&nbsp;A minimum of 3 years working in a sport development area.</p>\n\n\n\n<p>&nbsp;Must have a Valid drivers Licence.</p>\n\n\n\n<p>&nbsp;Must have a valid Safeguarding Certificate</p>\n\n\n\n<p></p>\n\n\n\n<p>KEY ATTRIBUTES</p>\n\n\n\n<p>The successful candidate needs to demonstrate a fundamental knowledge of Cricketing Fraternity/</p>\n\n\n\n<p>Business / Affiliates /Associate and CSA programmes set  up and requirements, Computer Literacy</p>\n\n\n\n<p>in Microsoft Suite (Excel, Power Point, Word, Database programmes) and Affiliates/Associate &amp;</p>\n\n\n\n<p>EPG Integrated Transformation.</p>\n\n\n\n<p>&nbsp;Planning &amp; Organising</p>\n\n\n\n<p>&nbsp;Conflict Management/Resoluton</p>\n\n\n\n<p>&nbsp;Problem Solving &amp; Analysis</p>\n\n\n\n<p>&nbsp;Deadline driven and self-motivated</p>\n\n\n\n<p>&nbsp;High level knowledge of the game of cricket</p>\n\n\n\n<p>&nbsp;Good communication skills and ability to confidently interact across all levels of stakeholder engagement</p>\n\n\n\n<p>&nbsp;Negotiation</p>\n\n\n\n<p>&nbsp;Handling pressure</p>\n\n\n\n<p></p>\n\n\n\n<p>APPLICATION</p>\n\n\n\n<p>Suitably qualified candidates are invited to apply for the position by submitting a comprehensive</p>\n\n\n\n<p>curriculum vitae (CV) with two (2) contactable references and certified copies of academic and all</p>\n\n\n\n<p>other qualifications to the HR Assistant: michellee@cricket.co.za by close of business on Friday 30</p>\n\n\n\n<p>May 2025. Starting date will be 01 July 2025. Only shortlisted candidates will be contacted.</p>\n\n\n\n<p>In making the final selection, consideration will be given to the Employment Equity of Western Province Cricket Association.</p>\n',
