    description:
      '\n<p><strong>The Person</strong></p>\n\n\n\n<p>We require a charismatic and inspirational candidate to oversee the coaching and development of all cricketers at Merchant Taylors across the age range. Cricket is a three-term sport at Merchant Taylors and the Head Cricket Professional will manage the day-to-day school cricket coaching programme over the course of the academic year, including the winter use of the Julian Hill Cricket Centre (JHCC), in conjunction with the Lettings Manager and Director of Cricket. Touring is central to the sports programme and cricket has the opportunity to tour every year with the U15 age group to the UAE and every three years with a major tour to destinations such as the Caribbean and Sri Lanka.</p>\n\n\n\n<p>Merchant Taylors has in excess of 20 cricket sides throughout the school. The Head Cricket Professional will be responsible for agreeing and executing a coaching plan across the school, taking responsibility for specific teams and sessions. The successful candidate will work closely with the Director of Cricket to ensure that there is a consistent, coherent pathway for player development within the school.</p>\n\n\n\n<p><strong>Main Duties and Responsibilities</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>To manage the day-to-day school cricket coaching programme over the course of the academic year, including the winter use of the Julian Hill Cricket Centre (JHCC), in conjunction with the Lettings Manager and Director of Cricket.</li>\n\n\n\n<li>Agreeing and executing a coaching plan across the school, taking responsibility for specific teams and sessions as agreed.</li>\n\n\n\n<li>Manage and communicate the day-to-day, weekly, and termly coaching programmes for cricket in conjunction with the Director of Cricket.</li>\n\n\n\n<li>To support our high-performance cricketers through the existing programme as well as 1:1 coaching where applicable.</li>\n\n\n\n<li>To support the continual professional development of cricket staff at Merchant Taylors in their coaching skills development.</li>\n\n\n\n<li>To be responsible, alongside the Director of Cricket, for the coaching of the schools top squads while also ensuring all teams are receiving a consistent and quality coaching provision across the age range.</li>\n\n\n\n<li>To contribute to the coaching of Merchant Taylors Prep cricketers in order to a ensure smooth transition to the Senior School.</li>\n\n\n\n<li>To work closely with the Director of Cricket to ensure that there is a consistent, coherent pathway for player development within the school.</li>\n\n\n\n<li>To help forge links with Merchant Taylors Prep, local clubs, feeder schools, local counties and other cricketing bodies to create a joined-up approach to cricket in the local community.</li>\n\n\n\n<li>To liaise with the Athletic Development Coach on appropriate physical development and training programmes for cricket players.</li>\n\n\n\n<li>To take a lead role in pre-season training across the age ranges and to attend overseas cricket tours at the discretion of the Director of Cricket and the Schools sports touring programme.</li>\n</ul>\n\n\n\n<p><strong>Read More</strong>:&nbsp;<a href="https://www.mtsn.org.uk/vacancies/support-positions" target="_blank" rel="noreferrer noopener">https://www.mtsn.org.uk/vacancies/support-positions</a></p>\n\n\n\n<h4 class="wp-block-heading">How to apply</h4>\n\n\n\n<p>Please complete an application form and an accompanying letter (maximum 500 words) outlining your suitability for the post; you may wish to include a brief CV.</p>\n',
