    description:
      '\n<p><strong>The Person</strong></p>\n\n\n\n<p>We require a charismatic and inspirational candidate to oversee all aspects of cricket at Merchant Taylors School. Cricket is a three-term sport at Merchant Taylors and the Director of Cricket, under the guidance of the Director of Sport, is responsible for delivering an aspirational strategy and<br>development plan to promote reputational recognition of, and value for, school cricket regionally and nationally.</p>\n\n\n\n<p>Touring is central to the sports programme and cricket has the opportunity to tour every year with the U15 age group to the UAE and every three years with a major tour to destinations such as the Caribbean and Sri Lanka.</p>\n\n\n\n<p>Merchant Taylors has in excess of 20 cricket sides throughout the school. The Director of Cricket is responsible for this extensive fixture list, with the aim of developing a progressive and challenging fixture programme at all levels. The Director of Cricket will also liaise with the Director of Sport over Games staffing.</p>\n\n\n\n<p>The successful candidate will also be expected to contribute to the GCSE and/or A-Level (AQA) Sports Science teaching.</p>\n\n\n\n<p><strong>Responsibilities as Director of Cricket</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>To line manage the Head Cricket Professional and liaise with that individual over the Schools cricket coaching programme.</li>\n\n\n\n<li>To oversee the cricket programme within the Julian Hill Cricket Centre in conjunction with the Head Cricket Professional (if appropriate).</li>\n\n\n\n<li>To develop school player pathway links with minor and major counties around the area.</li>\n\n\n\n<li>Liaise with the Director of Sport to develop a progressive and challenging fixture programme, which will form a major pillar of the long-term development plan for cricket.</li>\n\n\n\n<li>Management of the delivery of that development plan against agreed timescales.</li>\n\n\n\n<li>Overall responsibility for cricket staffing and fixture organisation.</li>\n\n\n\n<li>To take responsibility for age group competition entries in line with the cricket development plan.</li>\n\n\n\n<li>Working alongside the Director of Sport and Marketing Department on appropriate communications and a marketing strategy for cricket internally and externally, with boys, parents and beyond.</li>\n\n\n\n<li>Represent the interests of Cricket in the SCR and to the Senior Management Team.</li>\n\n\n\n<li>Liaison with the Grounds Manager and Ground Staff on all matters relating to pitch preparation<br>and management.</li>\n\n\n\n<li>Organisation of pre-season and overseas cricket tours.</li>\n\n\n\n<li>Overall responsibility for all Health and Safety associated with cricket, including updating relevant risk assessments.</li>\n\n\n\n<li>Overall responsibility for Annual Budget submission to Bursar and Director of Sport. Oversight of ongoing budgetary management.</li>\n\n\n\n<li>Liaise and build relationships with the OMT Cricket Club.</li>\n\n\n\n<li>To undertake cricket team coaching responsibilities as required.</li>\n\n\n\n<li>To stay attuned to cricket developments within a school setting and represent Merchant Taylors at regional and national events accordingly.</li>\n</ul>\n\n\n\n<p><strong>Read more:</strong>&nbsp;<a href="https://www.mtsn.org.uk/vacancies/teaching-staff" target="_blank" rel="noreferrer noopener">https://www.mtsn.org.uk/vacancies/teaching-staff</a></p>\n\n\n\n<h2 class="wp-block-heading"></h2>\n\n\n\n<h4 class="wp-block-heading">How to apply</h4>\n\n\n\n<p>Please complete an application form and an accompanying letter (maximum 500 words) outlining your suitability for the post; you may wish to include a brief CV.</p>\n',
    link: 'https://cricket.jobs/job-listing/director-of-cricket-merchant-taylors-school/',
