    jobTitle: 'Performance Pathway Analyst Internship',
    location: 'Location : United Kingdom',
    address:
      'Address : Craigleith Rise, Edinburgh, Scotland, EH4 3NT, United Kingdom',
    salary: 'Salary : Depending on experience',
    contractLength: 'Contract length : Full Time',
    closingDate: 'Closing date : May 22, 2025',
    logo: 'https://cricket.jobs/wp-content/uploads/2025/05/1-5.png',
    email: null,
    website: null,
    contactNumber: null,
    description:
      '\n<p><strong>Who are we?</strong></p>\n\n\n\n<p>Cricket Scotland is the national governing body for the sport of cricket in Scotland. We are committed to the promotion and development of the sport at all levels  from grassroots through to international representation. Our 20242028 strategy, Uniting People and Communities through Cricket, outlines our ambition to be an inclusive and high-performing organisation at the heart of Scottish sport and community life.</p>\n\n\n\n<p><strong>Internship Purpose</strong></p>\n\n\n\n<p>The Performance Pathway Analyst Internship has been created to provide matchday analyst support to Cricket Scotlands National and Regional Performance Pathway squads.</p>\n\n\n\n<p>Working as part of the pathway coaching team, the candidate will oversee the setup and running of the match day analyst systems and scoring platform, provide coaches with the data insights requested, and provide player development support.</p>\n\n\n\n<p><strong>Role Description</strong></p>\n\n\n\n<p>Roles &amp; Responsibilities</p>\n\n\n\n<ul class="wp-block-list">\n<li>Matchday setup of cameras &amp; analysis equipment at the allocated ground.&nbsp;</li>\n\n\n\n<li>Live coding across the pathway teams.</li>\n\n\n\n<li>&nbsp;Prepare match data in advance by ensuring fixtures are correctly set up in the scoring system.</li>\n\n\n\n<li>&nbsp;Assist with pre and post-match analysis of our performance &amp; opposition.</li>\n\n\n\n<li>Aid training sessions across the pathway, either filming or with data collection across the winter &amp; summer.</li>\n\n\n\n<li>Ensure any footage requested is uploaded to the CS analysis platform.</li>\n\n\n\n<li>Support CS comms teams with in-game and post-match data, assist with digital content output.</li>\n\n\n\n<li>Collate and share post-match information with coaches for player development purposes.</li>\n</ul>\n\n\n\n<p>Pre-Match Preparation</p>\n\n\n\n<ul class="wp-block-list">\n<li>Work with squad Head Coaches to confirm team details and in-game data requirements.</li>\n\n\n\n<li>Ensure that all player data is accurately populated on required scoring platforms.</li>\n\n\n\n<li>Engage with opposition teams and scorers to ensure full match preparation.</li>\n</ul>\n\n\n\n<p>General</p>\n\n\n\n<ul class="wp-block-list">\n<li>Perform any reasonable tasks as requested by the Head Coach, Head of Performance Pathway, or other Cricket Scotland staff.</li>\n\n\n\n<li>Support continuous improvement in the development of cricketers on the CS Performance Pathway.</li>\n</ul>\n\n\n\n<p><strong>Support Provided by Cricket Scotland</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>All equipment required to undertake the role</li>\n\n\n\n<li>Mentoring and training for the use of all analysis platforms used by Cricket Scotland</li>\n\n\n\n<li>Opportunities to work in the High Performance environment and at High Performance events.</li>\n\n\n\n<li>Opportunities to assist CS Comms teams in creative and data-led digital projects.</li>\n</ul>\n\n\n\n<p><strong>Person Specification</strong></p>\n\n\n\n<p>To be considered for this opportunity, you must be able to demonstrate the following skills, knowledge and abilities:</p>\n\n\n\n<p> Strong understanding of Microsoft products and IT skills.<br> Understanding of cricket.<br> Strong organisational and communication skills.<br> Experience setting up analyst equipment and coding.<br> Ability to work independently and under pressure.<br> Attention to detail and data accuracy.<br> Adaptability and problem-solving skills in a match environment.<br> Positive and professional approach to working with coaches, players, and officials.<br> Competence with Microsoft Office or data management software.<br> Commitment to equality, diversity, and inclusion.<br> Living in Scotland.<br> Flexible approach to working weekends and midweek fixtures during the summer.<br> Access to suitable transport options.</p>\n\n\n\n<p><strong>Time Commitment</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Regional fixtures: Predominantly weekends (MayJune) and midweek (July).</li>\n\n\n\n<li>National fixtures: Midweek and weekends (JuneSeptember).</li>\n</ul>\n\n\n\n<p><strong>Remuneration</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Travel expenses and administrative fees will be provided.</li>\n\n\n\n<li>Suitable Cricket Scotland clothing.</li>\n\n\n\n<li>Free access to any additional training required to fulfill the role.</li>\n\n\n\n<li>Accommodation provided where necessary for extended travel requirements.</li>\n</ul>\n\n\n\n<p><strong>Equality and Inclusion</strong></p>\n\n\n\n<p>Cricket Scotland is committed to creating a diverse and inclusive organisation that reflects the communities we serve.</p>\n\n\n\n<p>We actively welcome applications from individuals of all backgrounds, cultures, identities, and experiences.</p>\n\n\n\n<p>We believe that diversity of thought and lived experience strengthens our leadership and helps us better deliver our vision and strategy for the future of Scottish cricket. If you share our passion for the game and our ambition for its future, we encourage you to apply.</p>\n\n\n\n<p><strong>How to Apply</strong></p>\n\n\n\n<p>If you are interested in being considered for this opportunity, please submit:</p>\n\n\n\n<ul class="wp-block-list">\n<li>A copy of your CV; and</li>\n\n\n\n<li>A covering letter outlining your interest in this opportunity and the experience you would bring to the role.</li>\n</ul>\n\n\n\n<p>You will first be asked to provide some basic personal details before progressing to upload your application materials. We would also encourage all applications to complete our diversity monitoring form which can be found&nbsp;<a href="https://www.surveymonkey.com/r/M6M79ZF"><strong>HERE</strong></a>.</p>\n',
