      '\n<p>McBride Sport are supporting Sussex Cricket and the Sussex Cricket Foundation to appoint 3 new Trustees. These exciting opportunities offer the chance to be at the heart of promoting cricket for social good within Sussex.&nbsp;</p>\n\n\n\n<p>The Sussex Cricket Foundations aim is to actively change lives through cricket and to promote the sport to enhance social good within communities and so they are looking for a collaborative and motivated individuals who demonstrate enthusiasm and integrity and who will inspire a passion for cricket in all communities.&nbsp;</p>\n\n\n\n<p>The Board of Trustees are looking for applications from those with Board/Trustee experience coupled with previous skills in the following areas:</p>\n\n\n\n<p>Fundraising<br>Legal &amp; Governance<br>Charity Secretary Experience<br>Disability Sport&nbsp;</p>\n\n\n\n<p>The full applicant information pack can be found below. For more information please get in touch directly with Nigel Brewster &nbsp;<a href="mailto:nbrewster@mcbride-sport.com">nbrewster@mcbride-sport.com</a>&nbsp;or Julie Rushby &nbsp;<a href="mailto:jrushby@mcbride-sport.com">jrushby@mcbride-sport.com</a>.</p>\n\n\n\n<p><a href="https://sussexcricket.co.uk/sites/default/files/file-downloads/Sussex%20Trustee%20Candidate%20Pack%20v6.pdf" target="_blank" rel="noreferrer noopener">SCF Trustee Applicant Information Pack</a></p>\n',
