    jobTitle:
      'Safety and Security Manager  ICC Womens T20 World Cup 2026 (FTC until August 2026)',
    location: 'Location : United Kingdom',
    address: 'Address : United Kingdom',
    salary: 'Pay rate : Competitive',
    contractLength:
      'Contract length : Contract From August 2025 to August 2026',
    closingDate: 'Closing date : May 26, 2025',
    logo: 'https://cricket.jobs/wp-content/uploads/2025/05/1-7.jpeg',
    email: null,
    website: null,
    contactNumber: null,
    description:
      '\n<h3 class="wp-block-heading">Job Description</h3>\n\n\n\n<p><strong>PURPOSE</strong></p>\n\n\n\n<p>Were introducing this exciting role to support the ECBs delivery of the ICC Womens World T20 Cup in 2026. Working to the Head of Safety and Security and alongside our partners to deliver a safe and secure environment for players, spectators and staff throughout the tournament.</p>\n\n\n\n<p>The ICC Womens Twenty20 World Cup is the biennial global tournament for womens T20 international (T20I) cricket.&nbsp; The 2026 event will be the 10th edition of the womens tournament and is due to be hosted by the England &amp; Wales Cricket Board (ECB), delivered in partnership with the International Cricket Council (ICC).&nbsp; It will be the first time the event has returned to England &amp; Wales since the inaugural tournament in 2009  which England women won on home soil.</p>\n\n\n\n<p>Ambitions for the tournament are high.&nbsp; Within a fast moving womens sport landscape, our aim is not only to deliver the biggest womens cricket event in history, but to use it as a moment to redefine the standard for womens cricket attendances, reformulate revenue streams, and retune mindsets about what cricket is and who it is for. &nbsp;A moment in time to deliver long-term behaviour change for the whole sport and to secure the commercial future of womens cricket domestically and internationally.</p>\n\n\n\n<p>Joining the team behind the ICC Womens T20 World Cup 2026 is a chance to be part of a landmark moment in sporting history. &nbsp;This is your opportunity to collaborate with passionate individuals, deliver an exceptional sporting experience for players and fans, and leave a lasting impact on the future of womens cricket.</p>\n\n\n\n<p><strong>WHO WE ARE</strong></p>\n\n\n\n<p>The England and Wales Cricket Board is the national governing body for all cricket in England and Wales, supporting the game at every level  from grassroots to professional.</p>\n\n\n\n<p>We believe cricket is a force for good that can have an enduring impact on everyones lives and we have an ambitious strategy to inspire a generation to say cricket is a game for me. It provides great entertainment.&nbsp;It teaches important skills, both physical and social.&nbsp; It can help inclusion and social cohesion, bringing communities together.</p>\n\n\n\n<p>The ECB wants cricket to be the most inclusive sport in England and Wales. The ECB welcomes applications from all backgrounds to ensure we deliver our Inspiring Generations strategy and fulfil our purpose to connect communities and improve lives through cricket.</p>\n\n\n\n<p><strong>OUR VALUES</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li><strong>Run on respect.&nbsp;</strong>We ensure our game is based on fairness, respect and decency.&nbsp;</li>\n\n\n\n<li><strong>Embrace enjoyment.&nbsp;</strong>We make sure everyone finds fun in the game.&nbsp;</li>\n\n\n\n<li><strong>Build belonging.&nbsp;</strong>We actively include so everyone can find a place in the game.&nbsp;</li>\n</ul>\n\n\n\n<ul class="wp-block-list">\n<li><strong>Progress with purpose.&nbsp;</strong>We stay open to change to keep the game moving forward.&nbsp;</li>\n\n\n\n<li></li>\n</ul>\n\n\n\n<p><strong>YOULL LOVE THIS JOB IF</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>You enjoy working on varied high-profile sport events.</li>\n\n\n\n<li>You have a strong team ethic and can collaborate positively with multiple stakeholders.</li>\n\n\n\n<li>You are challenged by working in different environments over the course of an intensive season.</li>\n</ul>\n\n\n\n<p><strong>YOULL BE DOING</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Working ICC and match day venues to develop plans for the tournament</li>\n\n\n\n<li>Assist in budget planning and dealing with security contractors</li>\n\n\n\n<li>Working with the wider ECB Events team and partner agencies to establish and mitigate any threats and risks to players and officials.</li>\n\n\n\n<li>Developing and maintain excellent working relationships with stakeholders such as ICC, venue safety officers, operations directors and statutory agencies .</li>\n\n\n\n<li>Develop the Host City Security plans for all venues including plans for venues, hotels and all team movements.</li>\n\n\n\n<li>Providing venue specific briefings, debriefings, support and liaison between Team Security Liaison Officers within host venues</li>\n</ul>\n\n\n\n<p><strong>YOULL HAVE</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Experience of working within major sports events and preferably an understanding of elite womens sport</li>\n\n\n\n<li>Previous experience of working in a security environment with proven ability to deliver projects within fixed timeframes.</li>\n\n\n\n<li>Experience of managing security staff and delivering safety and security plans at major venues.</li>\n\n\n\n<li>Good understanding of Green Guide and regulatory environment around major sports venues.</li>\n\n\n\n<li>Excellent communication and administration skills.</li>\n\n\n\n<li>An ability to work remotely and flexibly.</li>\n</ul>\n\n\n\n<p><strong>YOULL RECEIVE</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Competitive salary</li>\n\n\n\n<li>Holiday  25 days a year</li>\n\n\n\n<li>Volunteering  2 days a year</li>\n\n\n\n<li>Pension  Non-contributory pension</li>\n\n\n\n<li>Private medical insurance</li>\n\n\n\n<li>Employee health cash-back plan</li>\n\n\n\n<li>Long-term sickness insurance</li>\n\n\n\n<li>Life assurance  four times your annual basic salary</li>\n\n\n\n<li>Enhanced family leave and pay</li>\n\n\n\n<li>OpenBlend  an innovative coaching and performance management tool</li>\n\n\n\n<li>People Academy  a range of programmes and initiatives to help you develop and reach your potential</li>\n\n\n\n<li>Employee Assistance Programme  24/7 confidential access to expert services including counselling, wellbeing, and independent legal and financial advice</li>\n\n\n\n<li>30% Castore discount</li>\n\n\n\n<li>Cycle 2 Work Scheme</li>\n\n\n\n<li>Season ticket loan</li>\n\n\n\n<li>Preferential access to tickets to England and The Hundred games</li>\n\n\n\n<li>Opportunities to be a part of and be involved in our Employee Resource groups: Women in Cricket Employee Network, Race in Cricket Employee Network, Social Equality in Cricket Employee Network, Cricketability Employee Network (formerly called the Disability in Cricket Employee Network) and LGBTQ+ Community in Cricket Employee Network</li>\n</ul>\n',
    link: 'https://cricket.jobs/job-listing/safety-and-security-manager-icc-womens-t20-world-cup-2026-ftc-until-august-2026/',
