    description:
      '\n<h3 class="wp-block-heading">Job Description</h3>\n\n\n\n<p><strong>PURPOSE</strong></p>\n\n\n\n<p>We have an exciting opportunity for the right person to join the Domestic Cricket Operations team as a Regulation Executive. The overall purpose of this role is to manage compliance of player eligibility regulations across the professional game.&nbsp;</p>\n\n\n\n<p><strong>WHO WE ARE</strong></p>\n\n\n\n<p>The England and Wales Cricket Board is the national governing body for all cricket in England and Wales, supporting the game at every level  from grassroots to professional.</p>\n\n\n\n<p>We believe cricket is a force for good that can have an enduring impact on everyones lives and we have an ambitious strategy to inspire a generation to say cricket is a game for me. It provides great entertainment.&nbsp;It teaches important skills, both physical and social.&nbsp; It can help inclusion and social cohesion, bringing communities together.</p>\n\n\n\n<p>The ECB welcomes applications from all backgrounds to ensure we deliver our Inspiring Generations strategy and fulfil our purpose to connect communities and improve lives through cricket.</p>\n\n\n\n<p><strong>OUR VALUES</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li><strong>Run on respect.&nbsp;</strong>We ensure our game is based on fairness, respect and decency.&nbsp;</li>\n\n\n\n<li><strong>Embrace enjoyment.&nbsp;</strong>We make sure everyone finds fun in the game.&nbsp;</li>\n\n\n\n<li><strong>Build belonging.&nbsp;</strong>We actively include so everyone can find a place in the game.&nbsp;</li>\n\n\n\n<li><strong>Progress with purpose.&nbsp;</strong>We stay open to change to keep the game moving forward.&nbsp;</li>\n</ul>\n\n\n\n<p><strong>YOULL LOVE THIS JOB IF</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>You are comfortable working independently and managing your own time.&nbsp;</li>\n\n\n\n<li>You have an interest in cricket.&nbsp;</li>\n\n\n\n<li>You thrive in a fast-paced environment.&nbsp;</li>\n\n\n\n<li>You value being part of a supportive and collaborative team.&nbsp;</li>\n</ul>\n\n\n\n<p><strong>YOULL BE DOING</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Assessing and processing player registration applications against player eligibility regulations for all domestic competitions (Professional County Cricket men and women &amp; The Hundred), and maintaining the relevant databases.&nbsp;</li>\n\n\n\n<li>Assessing and processing applications for Governing Body Endorsements (GBE), for migrant players and coaches under the Home Office Points Based System Managed Migration policy for Sport, for all professional domestic competitions.&nbsp;</li>\n\n\n\n<li>Supporting the delivery of the annual Hundred player Draft registration process to ensure only players who are eligible to enter can (as per eligibility rules and immigration rules  visa checking and/or GBE criteria assessment)&nbsp;</li>\n\n\n\n<li>Acting as primary contact for Professional County Cricket teams and Hundred teams, with regards to player eligibility queries.&nbsp;</li>\n\n\n\n<li>Acting as secondary contact, with regards to No Objection Certificate applications.&nbsp;</li>\n\n\n\n<li>Assisting other members of the Domestic Cricket Operations team with pre-season and in-season work as required.&nbsp;&nbsp;</li>\n</ul>\n\n\n\n<p><strong>YOULL HAVE</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>An excellent understanding of Professional Level cricket and its regulatory framework  men and women&nbsp;</li>\n\n\n\n<li>An excellent knowledge and understanding of Governing Body Endorsement processes in relation to migrant sportspeople.&nbsp;</li>\n\n\n\n<li>Ability to build and maintain good relationships with key stakeholders.&nbsp;</li>\n\n\n\n<li>Good computer literacy and experience of operating databases.&nbsp;</li>\n\n\n\n<li>A keen eye for detail.&nbsp;</li>\n\n\n\n<li>Good time management&nbsp;</li>\n</ul>\n\n\n\n<p><strong>YOULL RECEIVE</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Competitive salary</li>\n\n\n\n<li>Holiday  25 days a year</li>\n\n\n\n<li>Volunteering  2 days a year</li>\n\n\n\n<li>Pension  Non-contributory pension</li>\n\n\n\n<li>Private medical insurance</li>\n\n\n\n<li>Employee health cash-back plan</li>\n\n\n\n<li>Long-term sickness insurance</li>\n\n\n\n<li>Life assurance  four times your annual basic salary</li>\n\n\n\n<li>Enhanced family leave and pay</li>\n\n\n\n<li>OpenBlend  an innovative coaching and performance management tool</li>\n\n\n\n<li>People Academy  a range of programmes and initiatives to help you develop and reach your potential</li>\n\n\n\n<li>Employee Assistance Programme  24/7 confidential access to expert services including counselling, wellbeing, and independent legal and financial advice</li>\n\n\n\n<li>30% Castore discount</li>\n\n\n\n<li>Cycle 2 Work Scheme</li>\n\n\n\n<li>Season ticket loan</li>\n\n\n\n<li>Preferential access to tickets to England and The Hundred games</li>\n\n\n\n<li>Opportunities to be a part of and be involved in our Employee Resource groups: Women in Cricket Employee Network, Race in Cricket Employee Network, Social Equality in Cricket Employee Network, Cricketability Employee Network (formerly called the Disability in Cricket Employee Network) and LGBTQ+ Community in Cricket Employee NetworkOpenBlend  an innovative coaching and performance management tool</li>\n\n\n\n<li>People Academy  a range of programmes and initiatives to help you develop and reach your potential</li>\n\n\n\n<li>Employee Assistance Programme  24/7 confidential access to expert services including counselling, wellbeing, and independent legal and financial advice</li>\n\n\n\n<li>30% Castore discount</li>\n\n\n\n<li>Cycle 2 Work Scheme</li>\n\n\n\n<li>Season ticket loan</li>\n\n\n\n<li>Preferential access to tickets to England and The Hundred games</li>\n\n\n\n<li>Opportunities to be a part of and be involved in our Employee Resource groups: Women in Cricket Employee Network, Race in Cricket Employee Network, Social Equality in Cricket Employee Network, Cricketability Employee Network (formerly called the Disability in Cricket Employee Network) and LGBTQ+ Community in Cricket Employee Network</li>\n</ul>\n',
