    jobTitle:
      'Team Liaison Officer- New Zealand Women A (Secondments and Consultants considered)',
    location: 'Location : United Kingdom',
    address: 'Address : United Kingdom',
    salary: 'Pay rate : Competitive',
    contractLength: 'Contract length : Contract 47 days (02 June-31 June 2025)',
    closingDate: 'Closing date : May 23, 2025',
    logo: 'https://cricket.jobs/wp-content/uploads/2025/05/1-7.jpeg',
    email: null,
    website: null,
    contactNumber: null,
    description:
      '\n<h3 class="wp-block-heading">Job Description</h3>\n\n\n\n<p><strong>&nbsp;PURPOSE</strong></p>\n\n\n\n<p>The Team Liaison Officer acts as a primary contact day-to-day for travelling teams (players and management) on all operational matters such as training, hotels, transportation and any team needs that arise.&nbsp;</p>\n\n\n\n<p>As part of the Cricket Operations team, the successful Team Liaison Officer will report directly to the Head of England Womens Operations and liaise closely with the ECB Events Team including but not limited to Events, Safety &amp; Security, Venue Providers etc.&nbsp;</p>\n\n\n\n<p><strong>WHO WE ARE</strong></p>\n\n\n\n<p>The England and Wales Cricket Board is the national governing body for all cricket in England and Wales, supporting the game at every level  from grassroots to professional.</p>\n\n\n\n<p>We believe cricket is a force for good that can have an enduring impact on everyones lives and we have an ambitious strategy to inspire a generation to say cricket is a game for me. It provides great entertainment.&nbsp;It teaches important skills, both physical and social.&nbsp; It can help inclusion and social cohesion, bringing communities together.</p>\n\n\n\n<p>The ECB wants cricket to be the most inclusive sport in England and Wales. The ECB welcomes applications from all backgrounds to ensure we deliver our Inspiring Generations strategy and fulfil our purpose to connect communities and improve lives through cricket.</p>\n\n\n\n<p><strong>OUR VALUES</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li><strong>Run on respect.&nbsp;</strong>We ensure our game is based on fairness, respect and decency.&nbsp;</li>\n\n\n\n<li><strong>Embrace enjoyment.&nbsp;</strong>We make sure everyone finds fun in the game.&nbsp;</li>\n\n\n\n<li><strong>Build belonging.&nbsp;</strong>We actively include so everyone can find a place in the game.&nbsp;</li>\n\n\n\n<li><strong>Progress with purpose.&nbsp;</strong>We stay open to change to keep the game moving forward.&nbsp;</li>\n</ul>\n\n\n\n<p><strong>YOULL LOVE THIS JOB IF</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>You are passionate about providing a proactive operational support and maintain key relationships between ECB &amp; International Teams.&nbsp;</li>\n\n\n\n<li>You are a self-starter, and enjoy working on your own initiative&nbsp;</li>\n\n\n\n<li>You enjoy working at pace, with constantly changing priorities&nbsp;</li>\n</ul>\n\n\n\n<p><strong>YOULL BE DOING</strong></p>\n\n\n\n<p>Management&nbsp;</p>\n\n\n\n<ul class="wp-block-list">\n<li>Arranging the collection of player allowances with delivery to team management or safe custody.&nbsp;</li>\n\n\n\n<li>Liaising with management team and venue managers regarding practice times and requirements e.g. net bowlers, hydration, ice etc.&nbsp;</li>\n\n\n\n<li>Accompanying team to practice and provide assistance as deemed necessary.&nbsp;</li>\n\n\n\n<li>Liaising with ECB and local association on all issues relevant to the teams stay.&nbsp;</li>\n\n\n\n<li>Attending regular meetings as required with team management, actioning any requirements.&nbsp;</li>\n\n\n\n<li>Attending any meetings as required with ECB wider events and security staff.&nbsp;</li>\n\n\n\n<li>Attending to media request as agreed by team management.&nbsp;</li>\n\n\n\n<li>Assisting overseas media personnel whenever requested.&nbsp;</li>\n\n\n\n<li>Assisting in the reasonable organisation of leisure time activities for players&nbsp;</li>\n\n\n\n<li>Providing assistance to match referee as and when required.&nbsp;</li>\n\n\n\n<li>Assisting players and management with sundry requests e.g. gear, boot repairs, purchases, phone cards etc.&nbsp;</li>\n\n\n\n<li>Liaising with ECB and action various matters required.&nbsp;</li>\n</ul>\n\n\n\n<p>Transport&nbsp;</p>\n\n\n\n<ul class="wp-block-list">\n<li>Liaising with ECB Events Team &amp; Cricket Operations on the arrival and supporting as required for arriving travelling party.&nbsp;</li>\n\n\n\n<li>Attending to travel requirements for players and management staff, this includes but is not limited to transport to and from airports, to and from hotels to competition/ training venues.&nbsp;</li>\n\n\n\n<li>Assisting players leaving/ joining the team with issues such as ticketing, visas, passport, transport and ensuring any ECB medical guidance is adhered to.&nbsp;</li>\n\n\n\n<li>Assisting with processing and transport of baggage, along with distribution of information for travel and any later updates.&nbsp;</li>\n\n\n\n<li>Following up on any lost or delayed baggage queries.&nbsp;</li>\n\n\n\n<li>Liaising with any relevant freight or transport company re: hotels/ airport or baggage transfers.&nbsp;</li>\n\n\n\n<li>Liaising with transport company re: training and match transfers.&nbsp;</li>\n\n\n\n<li>Assisting with loading of kit and equipment on and off the coach or van transport.&nbsp;</li>\n\n\n\n<li>Liaising with Touring Team Management on issues involving transport and baggage.&nbsp;</li>\n\n\n\n<li>Organising and arrange transport for teams attending official functions.&nbsp;</li>\n</ul>\n\n\n\n<p>Accommodation&nbsp;</p>\n\n\n\n<ul class="wp-block-list">\n<li>Providing advance notice to concierge, hotel staff and ECB security of team and management arrivals and departure arrangements.&nbsp;</li>\n\n\n\n<li>Providing hotels with team rooming lists.&nbsp;</li>\n\n\n\n<li>At hotel, assist porters with identification and removal of baggage to players rooms. This may involve staying with unattended baggage for security purposes.&nbsp;</li>\n\n\n\n<li>Discussing with hotel management team requirements for team room facilities, food and drink.&nbsp;</li>\n\n\n\n<li>Providing laundry service option, arrange collection and delivery. Support any players or management with any queries on this service.&nbsp;</li>\n\n\n\n<li>Assisting players to locate lost items and follow up on unpaid accounts at hotels.&nbsp;</li>\n\n\n\n<li>Examining and confirming payment of all hotel accounts.&nbsp;</li>\n</ul>\n\n\n\n<p>Miscellaneous&nbsp;</p>\n\n\n\n<ul class="wp-block-list">\n<li>Be familiar with all members of the touring party&nbsp;</li>\n\n\n\n<li>Attend to usual work-related issues as required.&nbsp;</li>\n\n\n\n<li>Be on call and available to team 24 hours a day, 7 days a week.&nbsp;</li>\n</ul>\n\n\n\n<p><strong>YOULL HAVE</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>A knowledge of cricket and sports event management.&nbsp;</li>\n\n\n\n<li>Discretion in handling sensitive issues and confidential information.&nbsp;</li>\n\n\n\n<li>Ability to work autonomously and confidently make decisions and solve complex issues on the spot.&nbsp;</li>\n\n\n\n<li>Ability to demonstrate sensitivity, calmness and is politically, culturally, socially and religiously aware.&nbsp;</li>\n\n\n\n<li>Outstanding planning, communication and influencing skills.&nbsp;</li>\n\n\n\n<li>Strong understanding of venues, airlines, hotel operations and logistics.&nbsp;</li>\n\n\n\n<li>Flexibility to work in changing environments where requirements change at very short notice.&nbsp;</li>\n</ul>\n',
    link: 'https://cricket.jobs/job-listing/team-liaison-officer-new-zealand-women-a-secondments-and-consultants-considered/',
