    description:
      '\n<h3 class="wp-block-heading">Job Description</h3>\n\n\n\n<p><strong>PURPOSE</strong>&nbsp;</p>\n\n\n\n<p>The ECB are recruiting for a dynamic and highly organised Personal Assistant &amp; Accommodation Executive. You will be supporting the Managing Director of Competitions &amp; Major Events logistics. This role is important for the smooth running of the ECB, and will be at the heart of English cricket operations.&nbsp;</p>\n\n\n\n<p><strong>WHO WE ARE</strong>&nbsp;</p>\n\n\n\n<p>The England and Wales Cricket Board is the national governing body for all cricket in England and Wales, supporting the game at every level  from grassroots to professional.&nbsp;</p>\n\n\n\n<p>We believe cricket is a force for good that can have an enduring impact on everyones lives and we have an ambitious strategy to inspire a generation to say cricket is a game for me. It provides great entertainment. It teaches important skills, both physical and social.&nbsp; It can help inclusion and social cohesion, bringing communities together.&nbsp;</p>\n\n\n\n<p>The ECB wants cricket to be the most inclusive sport in England and Wales. The ECB welcomes applications from all backgrounds to ensure we deliver our Inspiring Generations strategy and fulfil our purpose to connect communities and improve lives through cricket.&nbsp;</p>\n\n\n\n<p><strong>&nbsp;OUR VALUES</strong>&nbsp;</p>\n\n\n\n<ul class="wp-block-list">\n<li>Run on respect. We ensure our game is based on fairness, respect and decency.&nbsp;</li>\n\n\n\n<li>Embrace enjoyment. We make sure everyone finds fun in the game.&nbsp;</li>\n\n\n\n<li>Build belonging. We actively include so everyone can find a place in the game.&nbsp;</li>\n\n\n\n<li>Progress with purpose. We stay open to change to keep the game moving forward.&nbsp;</li>\n</ul>\n\n\n\n<p><strong>YOULL LOVE THIS JOB IF</strong>&nbsp;</p>\n\n\n\n<ul class="wp-block-list">\n<li>You enjoy working with senior leaders and other stakeholders to ensure effective decision making.&nbsp;</li>\n\n\n\n<li>You want a variety of responsibilities and tasks.&nbsp;</li>\n\n\n\n<li>You enjoy working collaboratively within a fast-paced team environment.&nbsp;&nbsp;&nbsp;</li>\n</ul>\n\n\n\n<p><strong>YOULL BE DOING</strong>&nbsp;</p>\n\n\n\n<ul class="wp-block-list">\n<li>Managing the diary and corporate activities for the Managing Director  Competitions &amp; Major Events.&nbsp;&nbsp;</li>\n\n\n\n<li>Responsible for coordinating key meetings, organising domestic and overseas travel and other logistics, managing finance queries (expenses, POs, invoices and subscriptions).&nbsp;</li>\n\n\n\n<li>Supporting the Events Dept. in managing all match day accommodation throughout 2025/2026.&nbsp;</li>\n\n\n\n<li>Supporting at various meetings, including taking minutes, organising travel and accommodation and other essential arrangements.Providing assistance at events and conferences.&nbsp;&nbsp;</li>\n\n\n\n<li>Assisting the Managing Director  Competitions &amp; Major Events in preparations for the ICC Womens T20 World Cup 2026.&nbsp;&nbsp;</li>\n</ul>\n\n\n\n<p><strong>YOULL HAVE</strong>&nbsp;</p>\n\n\n\n<ul class="wp-block-list">\n<li>Experience as a personal / executive assistant.&nbsp;&nbsp;</li>\n\n\n\n<li>A track record of being highly organised, hitting tight deadlines and managing stressful situations.&nbsp;</li>\n\n\n\n<li>Excellent attention to detail.&nbsp;&nbsp;</li>\n\n\n\n<li>Good project management and planning skills. &nbsp;</li>\n\n\n\n<li>Ability to prioritise and multitask efficiently.&nbsp;&nbsp;</li>\n</ul>\n\n\n\n<p><strong>YOULL RECEIVE&nbsp;</strong>&nbsp;</p>\n\n\n\n<ul class="wp-block-list">\n<li>Competitive salary&nbsp;</li>\n\n\n\n<li>Holiday  25 days a year&nbsp;</li>\n\n\n\n<li>Volunteering  2 days a year&nbsp;</li>\n\n\n\n<li>Pension  Non-contributory pension&nbsp;</li>\n\n\n\n<li>Private medical insurance&nbsp;</li>\n\n\n\n<li>Employee health cash-back plan&nbsp;</li>\n\n\n\n<li>Long-term sickness insurance&nbsp;</li>\n\n\n\n<li>Life assurance  four times your annual basic salary&nbsp;</li>\n\n\n\n<li>Enhanced family leave and pay&nbsp;</li>\n\n\n\n<li>OpenBlend  an innovative coaching and performance management tool&nbsp;</li>\n\n\n\n<li>People Academy  a range of programmes and initiatives to help you develop and reach your potential&nbsp;</li>\n\n\n\n<li>Employee Assistance Programme  24/7 confidential access to expert services including counselling, wellbeing, and independent legal and financial advice&nbsp;</li>\n\n\n\n<li>30% New Balance discount&nbsp;</li>\n\n\n\n<li>Cycle 2 Work Scheme&nbsp;</li>\n\n\n\n<li>Season ticket loan&nbsp;</li>\n\n\n\n<li>Preferential access to tickets to England and The Hundred games&nbsp;</li>\n\n\n\n<li>Opportunities to be a part of and be involved in our Employee Resource groups: Women in Cricket Employee Network, Race in Cricket Employee Network, Social Mobility in Cricket Employee Network, Disability Employee Network and LGBTQ+ Community in Cricket Employee Network&nbsp;</li>\n</ul>\n',
