      '\n<p><strong>Information:</strong></p>\n\n\n\n<p>Lancefield Cricket Club seeking applications for Womens Senior Cricket Coach.<br>Fielding 2 Womens sides of equal importance to our mens teams, LCC has a strong focus on community, equity, inclusion and fostering a culture of success. This has been integral in terms of our on- field and off field achievements; finishing second and fourth within the GDCA.<br>A quickly growing club, we seek an applicant to develop our new and more experienced players abilities, fostering a high-performance, inclusive and equity-centred environment catering to our unique position as a club which has equal male and female members, many new to cricket.</p>\n\n\n\n<p><strong>Key Selection Criteria:</strong></p>\n\n\n\n<p>A zest for cricket and assisting player growth.<br>Can create dynamic training sessions which will help advance our players abilities in all aspects of the game, individually and as team members.<br>Willing to foster players love of the game and a spirit of fair play.<br>Communicate effectively with members of the club to craft supportive environments centred on transparency.<br>Playing and non-playing applicants welcomed. Experienced Coach preferred.</p>\n',
