    email: 'Sekelenik@cput.ac.za',
    website: null,
    contactNumber: '0795800302',
    description:
      '\n<p>The Cape Peninsula University of Technology wishes to invite applications for the following vacancy.</p>\n\n\n\n<p>Job Title: Cricket Club Coach (Seasonal Contract)</p>\n\n\n\n<p>Department: Sport, Arts &amp; Culture</p>\n\n\n\n<p>Campus/Location: Bellville Campus</p>\n\n\n\n<p>Job Type: Contract</p>\n\n\n\n<p>Salary Package: To be confirmed</p>\n\n\n\n<p>Advert Closing Date: 23/05/2025</p>\n\n\n\n<p>Job Purpose:</p>\n\n\n\n<p>The Cricket Coach will be responsible for ensuring optimal team preparation, player development,</p>\n\n\n\n<p>strategic planning, and contribute to the long-term success of CPUT Cricket.</p>\n\n\n\n<p>Responsibilities:</p>\n\n\n\n<p>&nbsp;Developing and implementing a coaching strategy aligned with CPUT Cricket performance plans.</p>\n\n\n\n<p>&nbsp;Planning and implementing team and individual performance programmes to ensure excellence</p>\n\n\n\n<p>in preparation and match performance.</p>\n\n\n\n<p>&nbsp;Conducting coaching sessions, pre-season programmes, player conditioning, and injury</p>\n\n\n\n<p>management.</p>\n\n\n\n<p>&nbsp;Developing and implementing effective training programs to enhance players performance.</p>\n\n\n\n<p>&nbsp;Identifying and nurturing talent, and recruiting student-athletes.</p>\n\n\n\n<p>&nbsp;Advising the Sport Development Officer (SDO) with regards to procurement of equipment, keep</p>\n\n\n\n<p>record of all equipment and report any damages/loss.</p>\n\n\n\n<p>&nbsp;Providing management and administrative assistance to the SDO and club.</p>\n\n\n\n<p>&nbsp;Ensuring that discipline is upheld in the team and carry out disciplinary sections where</p>\n\n\n\n<p>necessary.</p>\n\n\n\n<p>&nbsp;Holding meetings with the SDO to give and receive updates, submitting monthly and event</p>\n\n\n\n<p>specific reports.</p>\n\n\n\n<p>&nbsp;Demonstrating strong leadership, commitment, and dedication in alignment with the team and</p>\n\n\n\n<p>organisational values.</p>\n\n\n\n<p>&nbsp;Select the USSA Team and attend Tournaments.</p>\n\n\n\n<p>Minimum Requirements:</p>\n\n\n\n<p>&nbsp;Grade 12</p>\n\n\n\n<p>&nbsp;Minimum Level 1 CSA Coaching Certificate.</p>\n\n\n\n<p>&nbsp;Minimum of 3 years coaching experience.</p>\n\n\n\n<p>&nbsp;Proven experience in talent identification and player development.</p>\n\n\n\n<p>&nbsp;Ability to work with players from diverse backgrounds and cultures.Required Competencies (Skills, Knowledge and Behavioral Attributes)</p>\n\n\n\n<p>&nbsp;In-depth Cricket knowledge.</p>\n\n\n\n<p>&nbsp;Excellent tactical analysis and opponent assessment.</p>\n\n\n\n<p>&nbsp;Ability to design training programs and implement specialized training programs.</p>\n\n\n\n<p>&nbsp;Strong understanding of rules, regulations, and laws around Cricket and specific competitions.</p>\n\n\n\n<p>&nbsp;Ability to apply modern coaching and mentoring principles.</p>\n\n\n\n<p>&nbsp;Good mentoring and motivational skills to drive player development.</p>\n\n\n\n<p>&nbsp;Ability to travel with team during USSA tournaments.</p>\n\n\n\n<p>&nbsp;Good communication skills (verbal and written).</p>\n\n\n\n<p>&nbsp;Strong work ethic, self-motivation, and accountability.</p>\n\n\n\n<p>Application Procedure:</p>\n\n\n\n<p>Suitably qualified candidates are invited to apply for the position by submitting a comprehensive</p>\n\n\n\n<p>curriculum vitae (CV), cover letter, and certified copies of relevant qualifications and certifications to:</p>\n\n\n\n<p>Sekelenik@cput.ac.za&nbsp;by/before 23 May 2025. No application will be considered after the closing date</p>\n\n\n\n<p>or if it does not comply with at least the minimum requirements.</p>\n\n\n\n<p>Enquiries: Kholosa Sekeleni, Tel: 021 959 6335/079 580 0302</p>\n\n\n\n<p>The Cape Peninsula University of Technology reserves the right not to fill the advertised position.</p>\n\n\n\n<p></p>\n',
