    description:
      '\n<p>NWC DRAGONS PROFESSIONAL WOMENS TEAM</p>\n\n\n\n<p>CONTRACT PERIOD: 1 JUNE 2025  30 APRIL 2026</p>\n\n\n\n<p>North-West Cricket, with its headquarters situated at JB Marks Oval, 1 Loop Street, Potchefstroom, and subscribing to the principles of the Employment Equity Act, hereby invites an ambitious physiotherapist to apply for this vacancy.</p>\n\n\n\n<p>PURPOSE OF THE ROLE</p>\n\n\n\n<p>The successful candidate will act as the physiotherapist of the NW Dragons Womens Professional team and be responsible for the treatment and rehabilitation of injured contracted and non-contracted players. The team Physiotherapist will deliver all those responsibilities and protocols as set out in this Job Profile as well as the Provincial Player Injuries, Workload and Conditioning Policy.</p>\n\n\n\n<p>The role reports to the NWC Dragons Professional womens Team Head Coach.</p>\n\n\n\n<p>KEY ACCOUNTABILITIES</p>\n\n\n\n<ul class="wp-block-list">\n<li>Treatment and rehabilitation of injuries</li>\n\n\n\n<li>Administration of injuries and rehabilitation</li>\n\n\n\n<li>Recording of all injuries and rehabilitation as required on the Cricket Clinic player management system</li>\n\n\n\n<li>Stakeholder Management</li>\n</ul>\n\n\n\n<p>KEY JOR COMPFTENCIES</p>\n\n\n\n<ul class="wp-block-list">\n<li>High level of integrity and dependability with a strong sense of urgency and results orientation.</li>\n\n\n\n<li>Approachable</li>\n\n\n\n<li>Effective communicator</li>\n\n\n\n<li>Outstanding organisational and leadership abilities</li>\n\n\n\n<li>Excellent interpersonal skills</li>\n\n\n\n<li>High aptitude in decision-making and problem-solving</li>\n\n\n\n<li>Demonstrating good judgment</li>\n\n\n\n<li>Creativity</li>\n\n\n\n<li>Cognitive flexibility</li>\n\n\n\n<li>High emotional intelligence</li>\n\n\n\n<li>Service orientation</li>\n\n\n\n<li>Critical thinking ability</li>\n\n\n\n<li>Complex problem-solving abilit</li>\n</ul>\n\n\n\n<p></p>\n\n\n\n<p>Behavioural Skills:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Good judgment and decision-making abilities</li>\n\n\n\n<li>Emotionally intelligent, resilient, and good interpersonal skills</li>\n\n\n\n<li>Ability to influence and collaborate with others</li>\n\n\n\n<li>Development and nurturing focus</li>\n\n\n\n<li>Conscientious, self-motivated, and high work ethic</li>\n\n\n\n<li>Honesty, integrity &amp; confidentiality</li>\n\n\n\n<li>A team player and deadline-driven (critical)</li>\n\n\n\n<li>Accountability</li>\n</ul>\n\n\n\n<p>Competencies:</p>\n\n\n\n<p>Knowledge</p>\n\n\n\n<ul class="wp-block-list">\n<li>Intimate knowledge of sports physiotherapy</li>\n\n\n\n<li>Knowledge of the dynamics of sports team environments</li>\n\n\n\n<li>Sport injury rehabilitation trends and changes</li>\n\n\n\n<li>Transformation charter of SA</li>\n\n\n\n<li>Basic mentoring principles</li>\n</ul>\n\n\n\n<p>Skills</p>\n\n\n\n<ul class="wp-block-list">\n<li>Excellent planning and organising</li>\n\n\n\n<li>Scheduling</li>\n\n\n\n<li>Budgeting</li>\n\n\n\n<li>Conflict Management/Resolution</li>\n\n\n\n<li>Communication  Oral and written</li>\n\n\n\n<li>Report writing</li>\n</ul>\n\n\n\n<p>KEY QUALIFICATIONS AND REQUIREMENTS</p>\n\n\n\n<ul class="wp-block-list">\n<li>Suitable qualification (Bachelors Degree / National Diploma in Physiotherapy), preferably with a post-graduate qualification in sports physiotherapy.</li>\n\n\n\n<li>Registered with the Health Professions Council of South Africa (HPCSA) and a member of the South African Society of Physiotherapists (SASP).</li>\n\n\n\n<li>Has appropriate malpractice insurance.</li>\n\n\n\n<li>Engaged in and up-to-date continuous professional development (CPD), as reflected on the HPCSA CPD portal.</li>\n\n\n\n<li>Appropriate clinical experience  five (5) years, preferably having worked with professional athletes.</li>\n\n\n\n<li>Having worked in a cricket environment would be advantageous.</li>\n\n\n\n<li>Some management experience  providing reports, dealing with professional players, working with others in a team environment, and providing the lead in the medical team.</li>\n</ul>\n\n\n\n<p>KEY ATTRIRUTES</p>\n\n\n\n<ul class="wp-block-list">\n<li>Conscientious</li>\n\n\n\n<li>Self-motivated and high work ethic</li>\n\n\n\n<li>Confidentiality</li>\n\n\n\n<li>Reliable</li>\n</ul>\n\n\n\n<p>REMUNERATION</p>\n\n\n\n<p> The remuneration package for this position will be negotiated with the successful applicant.</p>\n\n\n\n<p>APPLICATIONS</p>\n\n\n\n<p>Only suitably qualified candidates are invited to apply for the position by submitting a comprehensive curriculum vitae (CV), with at least two (2) contactable references and certified copies of academic and other qualifications to Ms. Matlakala Mahasela at matlakalam@cricket.co.za by no later than Thursday, 22 May 2025. No late or incomplete applications will be accepted. Shortlisted candidates will undergo interviews and possibly additional assessments. Candidates who applied for the first round will not be<br>considered. In the interest of transparency, we kindly request applicants to disclose any family relations within the<br>organisation when submitting their job applications. In making the final selection, employment equity objectives will be considered. Only shortlisted candidates will be contacted. Should you not receive a response to your application within 14 days after the closing date, please consider your application as unsuccessful. North-West Cricket reserves the right not to make<br>an appointment.</p>\n',
