    jobTitle: 'STRENGTH & CONDITIONING COACH',
    location: 'Location : South Africa',
    address:
      'Address : Thabo Mbeki Way, Potchefstroom, North West, 2531, South Africa',
    salary: 'Salary : Depending on experience',
    contractLength: 'Contract length : 1 June 2025 to 30 April 2026',
    closingDate: 'Closing date : May 22, 2025',
    logo: 'https://cricket.jobs/wp-content/uploads/2025/05/North-West-Cricket.png',
    email: 'matlakalam@cricket.co.za',
