    description:
      '\n<p>&nbsp;NWC DRAGONS PROFESSIONAL WOMENS TEAM</p>\n\n\n\n<p>CONTRACT PERIOD: 1 JUNE 2025  30 APRIL 2026</p>\n\n\n\n<p>North-West Cricket (Non-Profit Company), with its headquarters situated in JB Marks Oval, 1 Loop Street, Potchefstroom, who subscribes to the principles of the Employment Equity, hereby invites an ambitious and motivated coach to apply for this vacancy.</p>\n\n\n\n<p>PURPOSE OF THE JOB</p>\n\n\n\n<p>The successful candidate will be required to primarily assist the NWC Dragons Professional womens team to reach and maintain a pre-determined fitness and strength level set by the team management to meet expectations on the field of play. This position also plays a key role in delivering the wider NORTH-WEST CRICKET strategy and goals.</p>\n\n\n\n<p>KEY ACCOUNTABILITIES</p>\n\n\n\n<ul class="wp-block-list">\n<li>Development of standardised strength, conditioning and assessment protocols for the North-West<br>Cricket Professional Womens team.</li>\n\n\n\n<li>Regularly testing the fitness status of all players using the established protocols.</li>\n\n\n\n<li>Plan and implement training programs to achieve optimal fitness and minimise the risk of injury, pre-<br>season and post-season.</li>\n\n\n\n<li>Creating and maintaining a database of relevant information relating to the NWC Dragons<br>Professional Womens team with a view to sharing such information with the Team Management and the Medical Committee. This includes keeping the relevant conditioning segment of the NWC DRAGONS professional womens team performance monitoring information system up to date.</li>\n\n\n\n<li>Interacting and providing relevant and regular information and reports of Team management, to<br>North West Cricket &amp; its sub-committees.</li>\n\n\n\n<li>Working together with the Teams physiotherapist in the rehabilitation of all player injuries, including during non-playing periods, and supporting injury risk reduction, injury management, and return to performance.</li>\n\n\n\n<li>Working with the NWC DRAGONS Professional womens team coaching staff in team preparation.</li>\n\n\n\n<li>Interacting closely with the Cricket South Africa Medical Committee.</li>\n\n\n\n<li>Initiating and assisting in research activity undertaken on behalf of North-West Cricket</li>\n\n\n\n<li>Prescribe and deliver individualised programmes to target players for the National Team</li>\n</ul>\n\n\n\n<p></p>\n\n\n\n<p>&nbsp;Knowledge &amp; Skills:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Understanding current best practice and scientific strengthening and conditioning methods, and being familiar with the published literature</li>\n\n\n\n<li>Ability to execute and deliver as per agreed KPIs and timelines</li>\n\n\n\n<li>A good understanding of integrated selection processes</li>\n\n\n\n<li>Strategic Thinker and Analyst</li>\n\n\n\n<li>Effective verbal and written communication skills</li>\n\n\n\n<li>Growth Mindset and Performance Oriented</li>\n\n\n\n<li>Highly organised individual who can work under pressure</li>\n\n\n\n<li>Working knowledge of all related legislation (including but not limited to the OHS Act, Safety at Sports and Recreational Act, etc.)</li>\n\n\n\n<li>Budget Planning, Expense Management &amp; Control</li>\n\n\n\n<li>Proficient in MS Office<br>Basic Management Skills</li>\n</ul>\n\n\n\n<p>Behavioural Skills:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Good judgment and decision-making abilities</li>\n\n\n\n<li>Emotionally intelligent, resilient, and good interpersonal skills</li>\n\n\n\n<li>Ability to influence and collaborate with others</li>\n\n\n\n<li>Development &amp; nurturing focus</li>\n\n\n\n<li>Conscientious, Self-motivated &amp; high work ethic</li>\n\n\n\n<li>Honesty, Integrity &amp; Confidentiality</li>\n\n\n\n<li>A Team Player and deadline-driven (critical)</li>\n\n\n\n<li>Takes Accountability</li>\n</ul>\n\n\n\n<p>Competencies:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Value Mindset  approach (all) work and deliverables with a value mindset (creating and adding value).</li>\n\n\n\n<li>Sense of Urgency  act promptly, decisively, and without delay.</li>\n\n\n\n<li>Initiates Action  proactively addresses problems, looks for ways to avert crises, anticipates and grasps opportunities to provide value add.</li>\n\n\n\n<li>Deliver Results  takes responsibility for achieving North-West Cricket, team, and own objectives, ensures completion and follow-through.</li>\n\n\n\n<li>Collaboration  build and maintain collaborative relationships, seek mutually beneficial solutions, and meaningful engagement with all stakeholders.</li>\n\n\n\n<li>Business Acumen  understands revenue generation, budget management, and cost containment in carrying out duties and responsibilities.</li>\n</ul>\n\n\n\n<p>KEY QUALIFICATIONS AND REQUIREMENTS</p>\n\n\n\n<ul class="wp-block-list">\n<li>A National diploma (NOF 6 qualification) in Sports Science or Biokinetics</li>\n\n\n\n<li>A B-Degree (NQF 7) qualification in Sports Science or Biokinetics will be an added advantage.</li>\n\n\n\n<li>A CSA-accredited Level 2 coaching certificate</li>\n\n\n\n<li>A minimum of three (3) years of fitness, strengthening, or equivalent experience in the sports industry</li>\n\n\n\n<li>Valid drivers license</li>\n\n\n\n<li>A valid Safeguarding certificate</li>\n\n\n\n<li>Be familiar with the latest coaching techniques and methods</li>\n\n\n\n<li>Strong written and oral communication skills.</li>\n</ul>\n\n\n\n<p>KEY ATTRIBUTES</p>\n\n\n\n<p>&nbsp;Development &amp; nurturing focus</p>\n\n\n\n<ul class="wp-block-list">\n<li>Conscientious</li>\n\n\n\n<li>Integrity</li>\n\n\n\n<li>Self-motivated and high work ethic</li>\n\n\n\n<li>Confidentiality</li>\n\n\n\n<li>Reliable</li>\n</ul>\n\n\n\n<p>REMUNERATION</p>\n\n\n\n<p>The remuneration package for this position will be negotiated with the successful applicant.</p>\n\n\n\n<p>APPLICATIONS</p>\n\n\n\n<p>Only suitably qualified candidates are invited to apply for the position by submitting a comprehensive curriculum vitac (CV), with at least two (2) contactable references and certified copies of academic and other qualifications to Ms. Maltakala Mahasela at matlakalam@cricket.co.za by no later than Thursday, 22 May 2025. No late or incomplete applications will be accepted. Shortlisted candidates will undergo interviews and possibly additional assessments. Candidates who applied for the first round will not be considered.</p>\n\n\n\n<p>In the interest of transparency, we kindly request applicants to disclose any family relations within the organisation when submitting their job applications.</p>\n\n\n\n<p>In making the final selection, employment equity objectives will be considered. Only shortlisted candidates will be contacted. Should you not receive a response to your application within 14 days after the closing date, please consider your application as unsuccessful. North-West Cricket reserves the right not to make an appointment</p>\n',
