    jobTitle: 'FREELANCE SPORTS JOURNALIST',
    location: 'Location : South Africa',
    address: 'Address : South Africa',
    salary: 'Salary : Depending on experience',
    contractLength: 'Contract length : TBC',
    closingDate: 'Closing date : May 25, 2025',
    logo: 'https://cricket.jobs/wp-content/uploads/2024/09/The-South-African-logo.png',
    email: 'freelance@blueskypublications.com',
    website: 'https://www.linkedin.com/jobs/view/4222242397',
    contactNumber: null,
    description:
      '\n<p><strong>Title:</strong> Sports Freelancer</p>\n\n\n\n<p><strong>Reporting to:</strong> Freelance Army Coordinator</p>\n\n\n\n<p><strong>Position:</strong> Freelancer</p>\n\n\n\n<p><strong>Location:</strong> Remote</p>\n\n\n\n<p><strong>OVERVIEW:</strong></p>\n\n\n\n<p>We are currently looking for a Sports Freelancer to join our publication.</p>\n\n\n\n<p><strong>Key purpose of this role:</strong></p>\n\n\n\n<p>To produce a minimum of 10 sports news articles per week.</p>\n\n\n\n<p><strong>Job description/key outputs:</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Write, publish, and quickly identify no less than 10 sports articles per week for online audiences.</li>\n\n\n\n<li>Cover major soccer events where needed and possible.</li>\n\n\n\n<li>Be comfortable reporting on significant South African sports (soccer, cricket, rugby, golf, athletics, etc.).</li>\n\n\n\n<li>Collaborate closely with your manager to develop story ideas.</li>\n\n\n\n<li>Generate fresh story ideas and help aggregate stories to provide a unique perspective and engaging read.</li>\n\n\n\n<li>Be proficient in using major social media platforms</li>\n\n\n\n<li>Live blog and live tweet sports events when required.</li>\n</ul>\n\n\n\n<p><strong>Requirements:</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>A degree or similar qualification in journalism and/or communications;</li>\n\n\n\n<li>At least two years of online writing experience with a reputable publisher.</li>\n\n\n\n<li>Two to three years of experience covering sports news.</li>\n\n\n\n<li>Experience with WordPress.</li>\n\n\n\n<li>A strong network of sources and newsmakers.</li>\n\n\n\n<li>Knowledge and understanding of search engine optimisation (SEO).</li>\n\n\n\n<li>Ability to work independently and communicate effectively.</li>\n\n\n\n<li>Solid knowledge of social media platform usage.</li>\n\n\n\n<li>A clear understanding of the South African context and its cultural nuances.</li>\n\n\n\n<li>Proficiency in analytics tools is an advantage.</li>\n</ul>\n\n\n\n<p>Please share your two-page CV and a link to your MuckRack account with freelance@blueskypublications.com.</p>\n\n\n\n<p><strong>If contacted, you will need to set aside 30 minutes for a timed assessment.</strong></p>\n',
