    jobTitle: 'Digital Media Consultant (Freelance/Part-Time)',
    location: 'Location : United Kingdom',
    address:
      'Address : Station Road, Wadhurst, England, TN5 7EP, United Kingdom',
    salary: 'Pay rate : circa 150 per day',
    contractLength: 'Contract length : Part Time',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/05/1-2.png',
    email: 'will@batforachance.org.uk',
    website: null,
    contactNumber: null,
    description:
      '\n<p><strong>About Bat For a Chance :</strong></p>\n\n\n\n<p>Bat For a Chance (BFAC) is on a mission to use cricket as a tool for social change. Established in 2019 by the then 14-year-old Will Gaffney, the charity has since its inception impacted over 20,000 individuals across 65 projects in 21 countries on four continents. BFAC has grown exponentially over the past 5 years and In December 2023, building on our success with the kit recycling concept, we launched the grant giving arm of the charity The Cricket For Social Change Fund.</p>\n\n\n\n<p>Our first CSC project was our joint venture to create a grass roots girls cricket infrastructure from scratch in the Caribbean which crucially also supports girls with their education and runs workshops for parents. At the same time we funded a bat refurbishment centre in Sri Lanka, that has taught over 150 children basic carpentry skills and repaired over 200 bats. More recently we have launched a coaching program in South Africa aiming to help tackle unemployment rates across the townships in cape town impact 100 participants in year 1.</p>\n\n\n\n<p>So far in 2025, we have supported projects across Europe impacting the refugee demographic in particular. Whilst also working closely in low socio economic areas with a passion for cricket across the UK. Alongside this we have run a sucessfull event and brand activation campaign with the likes of the Rajasthan Royals, Kumar Sangakkara, Coutts Private Bank, JM Finn, The Desert Vipers. As a charity our Ambassadors include Jofra Archer, Chris Jordan and Hayley Mathews.</p>\n\n\n\n<p><strong>Role Overview :</strong></p>\n\n\n\n<p>We are seeking a creative, organised and self-motivated freelance Digital Media advisor to manage and grow BFACs digital presence. This part-time role is ideal for someone with a strong digital skillset, a passion for storytelling, and an interest in working in sport or social development.</p>\n\n\n\n<p>You will work one day a week to lead our content strategy, manage key digital platforms, and ensure the BFAC brand is consistently represented across all channels</p>\n\n\n\n<p>Key Responsibilities :<br> Social Media Strategy and Managment:<br>Design and execute a comprehensive year-long social media strategy to build awareness and engagement.<br> Newsletter Production:<br>Create and send three newsletters per year to update supporters, partners and stakeholders.<br> Website Management:<br>Keep the BFAC website up to date with at least three content updates per year (news, events, impact stories, etc.).<br> Media Day Content:<br>Attend and create digital content at BFAC media days featuring professional cricketers and celebrities.<br> Podcast Development:<br>Help plan and relaunch the BFAC podcast into a more consistent and engaging format.<br> Analytics &amp; Insights:<br>Track and report on digital engagement metrics to inform ongoing strategy and improve performance.<br> Collaborations:<br>Work with external creatives and agencies such as DWB Designs and Fresh01 to support content production and campaign delivery.<br></p>\n\n\n\n<p><strong>Recruitment Policies :</strong></p>\n\n\n\n<p>Bat for a chance is committed to providing equitable opportunities. While we will always appoint on merit, we would particularly encourage applications from under  represented groups and communities in cricket  especially female or ethnically diverse people, or those who identify as having a disability.</p>\n\n\n\n<p>Bat for a chance is committed to safeguarding and protecting the children and young people that we work with. As such, all posts are subject to a safe recruitment process, including where applicable the disclosure of criminal records and vetting checks. The role of Digital Consultant meets the criteria of Regulated Activity and therefore any offer of employment will be conditional pending completion of a satisfactory Enhanced DBS certificate.</p>\n',
    link: 'https://cricket.jobs/job-listing/digital-media-consultant-freelance-part-time/',
