    email: 'hr@wacricket.com.au',
    website: null,
    contactNumber: '9265 7273',
    description:
      '\n<p>Are you passionate about grassroots sport and empowering coaches, umpires and volunteers? WA Cricket is seeking a dynamic and strategic Learning &amp; Capability Specialist to lead comprehensive training programs for Woolworths Cricket Blast volunteers, Community Coaches, and Umpires.<br><br>This is a unique opportunity to shape the future of community cricket by designing engaging learning pathways that inspire and support the people at the heart of the game.<br><br><strong>About the role</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Establish and support a network of Coach Developers and Coaching Coordinators to expand coach education at the grassroots level.</li>\n\n\n\n<li>Design and deliver an umpire training and accreditation program as part of a broader state umpiring strategy.</li>\n\n\n\n<li>Lead the development and delivery of training programs for Woolworths Cricket Blast Coordinators and volunteers, with a focus on supporting new and underperforming programs.</li>\n\n\n\n<li>Collaborate with Area Teams and High Performance to create a targeted coach development strategy across all Junior Cricket stages.</li>\n\n\n\n<li>Assist in growing the number of female coaches and officials in partnership with the Female Participation and Engagement Specialist.</li>\n\n\n\n<li>Use data to identify and address volunteer education needs across clubs in partnership with Area Teams.</li>\n\n\n\n<li>Serve as the key contact for Cricket Australia regarding national coach, umpire, and club education initiatives.</li>\n\n\n\n<li>Oversee the use of learning management systems to streamline communication and improve training outcomes.</li>\n</ul>\n\n\n\n<p><strong>A great fit for this role looks like</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Highly developed interpersonal and communication skills.</li>\n\n\n\n<li>Demonstrated experience in the development of adult training programs.</li>\n\n\n\n<li>Highly developed presentation and facilitation skills.</li>\n\n\n\n<li>Certification in a Community Coach Accreditation Course (Level One) or equivalent coach accreditation.</li>\n\n\n\n<li>Demonstrated relationship building abilities.</li>\n\n\n\n<li>Demonstrated administration and organisational skills.</li>\n\n\n\n<li>Passionate about growing the game of cricket in WA.</li>\n\n\n\n<li>Working with Children Card.</li>\n\n\n\n<li>C class drivers licence with own reliable transport.</li>\n</ul>\n\n\n\n<p><strong>The perks!</strong><br>Our people benefits include Membership passes to all cricket games at the WACA Ground and at Optus Stadium, onsite gym access, Birthday Leave, salary packaging, employee recognition program, discounts on cricket merchandise, corporate health insurance discount, corporate partner discount, flexible work arrangements and professional development opportunities.<br><br><strong>A little bit about us</strong><br>At WA Cricket we aim to be leaders in Australian sport and pride ourselves on developing inspiring players, creating a fun and inclusive community and delivering social outcomes through cricket. The iconic WACA Ground is home to the Perth Scorchers, WA Mens and Womens Teams and the WA Cricket Foundation.<br><br><strong>Our Culture</strong><br>At WA Cricket we are committed to nurturing a diverse and inclusive workforce and offer equal opportunities to all.&nbsp;We embrace diversity and encourage applications from people of all ages, ethnicities, abilities, sexual orientations and gender identities. We are committed to providing a safe environment for children across Australian Cricket, and we also offer a flexible work environment for all employees. We measure ourselves on our behaviours of Excellence, Collaboration, Inclusion and Celebration.</p>\n',
    link: 'https://cricket.jobs/job-listing/learning-capability-specialist/',
