    jobTitle: 'Cricket Manager  South Metro',
    location: 'Location : Australia',
    address: 'Address : East Perth, Western Australia, 6004, Australia',
    salary: 'Salary : Depending on experience',
    contractLength: 'Contract length : Full Time',
    closingDate: 'Closing date : May 21, 2025',
    logo: 'https://cricket.jobs/wp-content/uploads/2025/05/1-1.jpeg',
    email: 'hr@wacricket.com.au',
    website: null,
    contactNumber: '9265 7273',
    description:
      '\n<p>WA Cricket is on a mission to increase sustainable participation in cricket by building strong networks and engaging stakeholders within our community in the Metropolitan South region.<br><br>The Cricket Manager role is primarily responsible for supporting and interacting with junior community cricket clubs and their volunteers within the region to strengthen the games infrastructure and drive growth.<br><br><strong>About the role and Key Responsibilities</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Manage Participation and club development activities across the Metropolitan South region to increase the recruitment and retention of participants across all forms of the game.</li>\n\n\n\n<li>Develop recruitment and retention strategies for implementation within the defined geographical region which maximize participation and links to talent development, promoting the implementation of the junior cricket pathway across the different stages of cricket.</li>\n\n\n\n<li>Build, maintain and leverage key strategic regional stakeholder relationships (government, business, multicultural groups, clubs, associations, schools, local media etc).</li>\n\n\n\n<li>Support the Junior cricket pathway and provide support to clubs delivering the Woolworths Cricket Blast program with a focus on 5-12 year olds.</li>\n\n\n\n<li>Support Participation Officers assisting with the transition of students from schools into community clubs.</li>\n\n\n\n<li>Provide education and training opportunities for volunteers including the delivery of community coaching courses, Woolworths Cricket Blast (WWCB) training and training in systems (PlayHQ).</li>\n\n\n\n<li>Provide leadership and management for both casual and unpaid resources involved in the delivery of programs within the region.</li>\n\n\n\n<li>Develop operational plans that ensure the effective and sustainable operation of the Metropolitan South region.</li>\n\n\n\n<li>Work collaboratively with clubs and associations to ensure all child safeguarding (policies, systems and procedures) are implemented effectively.</li>\n\n\n\n<li>Work collaboratively with clubs and associations to ensure all infrastructure (guidelines, systems and strategies) are implemented effectively</li>\n</ul>\n\n\n\n<p><strong>A great fit for this role looks like</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Tertiary qualifications in Education, Sport Management or Management are desirable.</li>\n\n\n\n<li>You know how to connect, collaborate and create impactful relationships.</li>\n\n\n\n<li>The understanding of how community clubs and associations operate and the challenges their volunteers face.&nbsp;</li>\n\n\n\n<li>A proven ability to work independently and consistently meet deadlines.</li>\n\n\n\n<li>Ability to drive, plan and implement strategic objectives.</li>\n\n\n\n<li>A current and valid Working with Children Card.</li>\n\n\n\n<li>A C class drivers license.</li>\n</ul>\n\n\n\n<p><strong>The perks</strong>!</p>\n\n\n\n<p>Our people benefits include Membership passes to all cricket games at the WACA Ground and at Optus Stadium, onsite gym access, Birthday leave, salary packaging, employee recognition program, discounts on cricket merchandise, corporate health insurance discount, corporate partner discount, flexible work arrangements and professional development opportunities.<br><br><strong>A little bit about us&nbsp;&nbsp;</strong></p>\n\n\n\n<p><strong>&nbsp;</strong>At WA Cricket we aim to be leaders in Australian sport and pride ourselves on developing inspiring players, creating a fun and inclusive community and delivering social outcomes through cricket. The iconic WACA Ground is home to the Perth Scorchers, WA Mens and Womens Teams and the WA Cricket Foundation.</p>\n\n\n\n<p><strong>Our Culture</strong></p>\n\n\n\n<p>At WA Cricket we are committed to nurturing a diverse and inclusive workforce and offer equal opportunities to all.&nbsp;We embrace diversity and encourage applications from people of all ages, ethnicities, abilities, sexual orientations and gender identities.&nbsp; We are committed to providing a safe environment for children across Australian Cricket and we also offer a flexible work environment for all employees. &nbsp;We measure ourselves on our values of Excellence, Collaboration, Inclusion and Celebration.</p>\n',
