    jobTitle: 'Sponsorship and Partnership Manager',
    location: 'Location : United Kingdom',
    address:
      'Address : Saint James Street, Taunton, England, TA1 1JT, United Kingdom',
    salary: 'Pay rate : 32,000 - 36,000 per annum',
