    description:
      '\n<p><strong>Welcome</strong></p>\n\n\n\n<p><br>Cricket has a unique ability to connect communities, improve lives and eradicate social barriers. Everything that Leicestershire &amp; Rutland Cricket Foundation is working<br>towards; every community programme, recreational team and inclusive opportunity that is on offer allows us to make great strides towards enhancing and extending the reputation, reach and profile of the game and Leicestershire Cricket.</p>\n\n\n\n<p><br>However, we need and want to do better. The Leicestershire &amp; Rutland Cricket Foundation is an integral member of the Leicestershire Cricket family. By inspiring participation and community engagement through cricket, we will improve lives across the County<br>and build trust and following.</p>\n\n\n\n<p>We live in vibrant and multi-cultural Counties and its vital that we rightly reflect, represent, and most importantly reach our diverse community. We want to be visible in the community to show that theres a place in the game for everyone  a real sense of belonging.</p>\n\n\n\n<p><br>Founded on exceptional governance and leadership, we will use cricket as a vehicle for good in peoples and communities lives, making the game accessible for all in our leagues, clubs, communities and schools so that more people can experience and enjoy everything that cricket has to offer. Thank you for your interest in becoming our Cricket Development Coach (Disability).</p>\n\n\n\n<p><strong>Purpose</strong></p>\n\n\n\n<p>To connect communities and improve lives through the power of cricket.</p>\n\n\n\n<p>We want to engage and excite people across the diverse communities we serve, giving everyone the opportunity to enjoy cricket.</p>\n\n\n\n<p><strong>Mission</strong></p>\n\n\n\n<p>To get bats and balls into more hands and introduce our communities to the power of cricket.</p>\n\n\n\n<p>We want the game to be inclusive for all, inspiring every generation to get involved and fulfil their potential.</p>\n\n\n\n<p><strong>Our values</strong></p>\n\n\n\n<p>Sharing positivity: Bringing positive-orientated thoughts, ambitions, beliefs, and attitudes.</p>\n\n\n\n<p>Caring at our core: Promoting a strong sense of community, leaving participants feeling valued, respected and connected.</p>\n\n\n\n<p>Elevating everybody: Empowering communities with the tools to grow.</p>\n\n\n\n<p>A welcoming environment for all: Creating a community for people to bring their best.</p>\n\n\n\n<p><strong>Responsibilities</strong></p>\n\n\n\n<p> Being a full, active and supportive member of the Cricket Participation team that will deliver the cricket participation element of the L&amp;RCFs strategy.</p>\n\n\n\n<p> Deliver coaching experiences that drive growth in accessibility and participation in disability cricket opportunities across the County.</p>\n\n\n\n<p> Deliver programmes and competitions to grow opportunities for adults and juniors to play disability cricket</p>\n\n\n\n<p> Identify facilities access and areas of demand to grow opportunities for adults and juniors to play disability cricket</p>\n\n\n\n<p> Help to deliver disability cricket volunteer workforce development within recreational clubs and hubs across the county.</p>\n\n\n\n<p> Deliver coaching sessions in schools and SEND settings</p>\n\n\n\n<p> Develop school to club / hub partnerships that connect participants to their optimal next cricket experience</p>\n\n\n\n<p><strong>Behaviours</strong></p>\n\n\n\n<p> Always be respectful of others, behave in a nondiscriminatory manner with an understanding of the Equality Act 2010.</p>\n\n\n\n<p> Willing and able to deal with people professionally at all levels and from a variety of backgrounds.</p>\n\n\n\n<p> Bring energy and enthusiasm to the role and operate to our clear values and behaviours.</p>\n\n\n\n<p> Hard working with a can-do attitude.</p>\n\n\n\n<p> Willingness to learn and adapt to an ever changing environment.</p>\n\n\n\n<p> A positive, professional, friendly and supportive manner.</p>\n\n\n\n<p> Ability to relate and empathise with clubs, leagues, volunteers and players.</p>\n\n\n\n<p><strong>Safeguarding</strong></p>\n\n\n\n<p>Leicestershire &amp; Rutland Cricket Foundation is committed to safeguarding and protecting children, young people and adults at risk to enjoy the game of cricket, both recreational and/or professional formats of the game. All stages of recruitment will follow Safer Recruitment guidelines. References will be sought upon offer of employment and depending on job role, background checks may be required. Upon commencement of employment, all new starters are required to undertake ECB and Club safeguarding training (level dependent on role undertaken) and read and understand Safe Hands guidance and our Club safeguarding policies and procedures.</p>\n\n\n\n<p><strong>Benefits</strong><br>33 days annual leave inclusive of bank holidays<br>Company Pension Scheme<br>Life Assurance<br>Statutory Sick Pay<br>Flexible working<br>Electric Car salary sacrifice<br>On site parking<br>Branded kit</p>\n',
