    description:
      '\n<h2 class="wp-block-heading">The Opportunity</h2>\n\n\n\n<p>A very exciting opportunity has arisen within our Cricket department as we look to recruit a Cricket Operations Coordinator. This role will provide support and resilience to the Cricket Operations function across the Mens XI, Womens XI and Talent Pathway. This role will positively contribute towards the overall success of the teams by providing operational and administrative support.</p>\n\n\n\n<p>Reporting to the Mens Cricket Operations Manager, this role will involve:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Providing match day support to both the Mens and Womens Cricket Operations Manager when required</li>\n\n\n\n<li>Assist with key pre-season tasks such as kit preparation and distribution ahead of and throughout the season</li>\n\n\n\n<li>Help with key administrative tasks such as travel, accommodation and other logistical support</li>\n\n\n\n<li>Provide support to the Talent Pathway as and when required</li>\n</ul>\n\n\n\n<p>For further information about the role and for further essential and desirable criteria, you can access the job description&nbsp;<a href="https://yorkshireccc.com/wp-content/uploads/2025/05/Cricket-Operations-Coordinator-Job-Description.pdf" target="_blank" rel="noreferrer noopener">here</a>.</p>\n\n\n\n<h2 class="wp-block-heading">Working at The Yorkshire County Cricket Club</h2>\n\n\n\n<p>Working at Yorkshire Cricket, you will benefit from the following:</p>\n\n\n\n<p></p>\n\n\n\n<ul class="wp-block-list">\n<li>Health cash plan provider</li>\n\n\n\n<li>25 days annual leave plus statutory bank holidays which increases based on years of service</li>\n\n\n\n<li>Pension contributions</li>\n\n\n\n<li>Yorkshire County Cricket Club membership</li>\n\n\n\n<li>Access to employee assistance programmes</li>\n\n\n\n<li>Enhanced parental leave</li>\n\n\n\n<li>Volunteering leave</li>\n\n\n\n<li>Wellbeing and cultural leave</li>\n</ul>\n\n\n\n<h2 class="wp-block-heading">Our Commitment</h2>\n\n\n\n<p>Yorkshire Cricket is committed to safeguarding children and adults. Due to this role being in regulated activity, the postholder will be required to undertake an ECB Enhanced Disclosure and Barring Service (DBS) Check; it is a criminal offence for a barred&nbsp;person to attempt to engage with vulnerable groups. The possession of a criminal record will not necessarily prevent an applicant from obtaining this post, as all cases are judged individually according to the nature of the role and information provided.</p>\n',
    link: 'https://cricket.jobs/job-listing/cricket-operations-coordinator/',
