      '\n<p><strong>Information:</strong></p>\n\n\n\n<p>Role Overview<br>The Mens Assistant Coach will support the Mens Coach in developing players skills, implementing training programs, and fostering a positive team environment. This role is crucial in ensuring players have a rewarding experience while improving their cricketing abilities.</p>\n\n\n\n<p>Key Responsibilities<br> Coaching and Player Development<br> Deliver squad, small group, and one-on-one coaching sessions<br> Assist in developing and implementing pre-season and in-season training programs<br> Support players in improving their technique, skills, mental preparation, and fitness<br> Provide clear, empathetic, and honest feedback to players</p>\n\n\n\n<p>Further information detailed upon interest of candidates.</p>\n\n\n\n<p><strong>Key Selection Criteria:</strong></p>\n\n\n\n<p> Cricket Australia coaching accreditation (Level 1)<br> Strong knowledge of cricket techniques, tactics, and rules<br> Excellent communication and interpersonal skills<br> Ability to work effectively with players of varying skill levels and ages</p>\n\n\n\n<p>Desired Attributes<br> Passion for cricket and player development<br> Adaptability and willingness to learn new coaching methods<br> Strong organizational and time management skills<br> Ability to work collaboratively in a team environment</p>\n',
