    description:
      '\n<p><strong>Information:</strong></p>\n\n\n\n<p>St Davids Cricket Club is seeking a senior head coach (playing or non-playing) for the 2025/26 season and beyond. This is a fantastic opportunity to join a great family-oriented club with a strong and determined senior squad seeking success at all levels.<br>The club fields 4 senior sides, a Tuesday night T20 side coming off a premiership in division 2, as well as a robust junior program and Woolworths Blast program. We are on the hunt for an determined and experienced individual who is looking to develop a talented young squad and foster a positive and hard-working culture.</p>\n\n\n\n<p><strong>Key Selection Criteria:</strong></p>\n\n\n\n<p> Proven leadership and experience in a high-level cricket environment<br> Guidance to our senior players and leaders<br> Mentor and support the development of our young players<br> Develop a senior program that raises training and playing standards<br> Organise training sessions and gameday warmups<br> Assist with team selection each round</p>\n',
