    website: null,
    contactNumber: null,
    description:
      '\n<p>Cricket Tasmania is the governing body for cricket in Tasmania and promotes and develops the game of cricket throughout the State. Our purpose is to inspire, engage and entertain our communities on and beyond the field of play.&nbsp; At Cricket Tasmania we strive to be a leader in sport and entertainment, delivering memorable experiences for all communities, and recognise our people are critical in achieving this vision.</p>\n\n\n\n<p><strong>About the role</strong><br>We are seeking Casual Chefs to join our Hurricanes Caf team at Ninja Stadium. The Hurricanes Caf is open 7 days a week serving breakfast and lunch. The role is responsible for preparing, cooking and serving food, ensuring a high level of quality and presentation is always achieved.</p>\n\n\n\n<p><strong>About you</strong><br>You have a Cert III or above in Commercial Cookery.<br>You have completed Food Safety Supervisor Training.<br>You have a Tasmanian WVVP for employment or willingness to obtain.<br>You have experience working independently in a kitchen environment.<br>You can work flexible hours, including weekends and public holidays.<br>You are organised and can communicate effectively with front of house staff and customers.<br>You have strong attention to detail and can contribute effectively in a team environment.<br>You have a calm manner and the ability to cope well a busy hospitality environment.</p>\n\n\n\n<p><strong>Additional Information</strong><br>Applications will be reviewed as they are submitted. We are committed to finding the right candidates for this role. As such, we may close the application process earlier than the listed deadline if we find suitable candidates. We encourage you to apply as soon as possible to be considered.</p>\n\n\n\n<p>For further information or to request a copy of the position description, please email <a href="mailto:ctpac@crickettas.com.au">ctpac@crickettas.com.au</a>.</p>\n\n\n\n<p>At Cricket Tasmania, we embrace diversity in gender, age, ethnicity, disability, religion and sexual orientation and we are committed to providing a safe environment for children across Australian Cricket.</p>\n',
