    description:
      '<p class="" data-start="201" data-end="545">We are seeking an experienced Cricket Coach to lead training sessions, develop player skills, and build a winning team culture. The ideal candidate will have a strong understanding of the game, excellent communication skills, and a proven track record of coaching at club professional levels.</p>\n<p class="" data-start="547" data-end="574"><strong data-start="547" data-end="572">Key Responsibilities:</strong></p>\n<ul data-start="575" data-end="746">\n<li class="" data-start="575" data-end="621">\n<p class="" data-start="577" data-end="621">Plan and conduct regular practice sessions</p>\n</li>\n<li class="" data-start="622" data-end="665">\n<p class="" data-start="624" data-end="665">Provide technical and tactical guidance</p>\n</li>\n<li class="" data-start="666" data-end="708">\n<p class="" data-start="668" data-end="708">Monitor player performance and fitness</p>\n</li>\n<li class="" data-start="709" data-end="746">\n<p class="" data-start="711" data-end="746">Foster team spirit and discipline</p>\n</li>\n</ul>\n<p class="" data-start="748" data-end="767"><strong data-start="748" data-end="765">Requirements:</strong></p>\n<ul data-start="768" data-end="916">\n<li class="" data-start="768" data-end="828">\n<p class="" data-start="770" data-end="828">Coaching certification (Level 2 or higher preferred)</p>\n</li>\n<li class="" data-start="829" data-end="869">\n<p class="" data-start="831" data-end="869">Prior coaching or playing experience is an added advantage</p>\n</li>\n<li class="" data-start="870" data-end="916">\n<p class="" data-start="872" data-end="916">Strong leadership and interpersonal skills</p>\n</li>\n</ul>\n',
