    jobTitle: 'Commercial Partnerships Sales Manager',
    location: 'Location : United Kingdom',
    address: 'Address : Manchester, England, United Kingdom',
    salary: 'Pay rate : Competitive',
    contractLength: 'Contract length : Full Time',
    closingDate: 'Closing date : May 26, 2025',
    logo: 'https://cricket.jobs/wp-content/uploads/2025/04/1-28.png',
    email: null,
    website: null,
    contactNumber: null,
    description:
      '\n<h2 class="wp-block-heading">About Us</h2>\n\n\n\n<p>We are Lancashire Cricket Club, a prestigious sports organization based in Manchester. We are currently seeking a talented and driven Commercial Partnerships Sales Manager to join our team.</p>\n\n\n\n<h2 class="wp-block-heading">Responsibilities:</h2>\n\n\n\n<ul class="wp-block-list">\n<li>Develop and implement strategic sales plans to achieve revenue targets.</li>\n\n\n\n<li>Identify and secure new commercial partnerships and sponsorships.</li>\n\n\n\n<li>Build and maintain strong relationships with existing partners.</li>\n\n\n\n<li>Create compelling sales pitches and presentations.</li>\n\n\n\n<li>Negotiate contracts and agreements with potential partners.</li>\n\n\n\n<li>Collaborate with marketing and events teams to deliver integrated partnership solutions.</li>\n\n\n\n<li>Stay up-to-date with industry trends and competitor activities.</li>\n\n\n\n<li>Provide regular reports on sales performance and pipeline status.</li>\n</ul>\n\n\n\n<h2 class="wp-block-heading">Qualifications:</h2>\n\n\n\n<ul class="wp-block-list">\n<li>Proven experience in B2B sales, preferably in the sports or leisure industry.</li>\n\n\n\n<li>Demonstrated track record of meeting and exceeding sales targets.</li>\n\n\n\n<li>Strong negotiation and communication skills.</li>\n\n\n\n<li>Ability to work effectively in a fast-paced and dynamic environment.</li>\n\n\n\n<li>Excellent networking and relationship-building abilities.</li>\n\n\n\n<li>Driven, ambitious, and results-oriented mindset.</li>\n\n\n\n<li>Bachelors degree in Business, Marketing, or related field.</li>\n</ul>\n\n\n\n<h2 class="wp-block-heading">Benefits:</h2>\n\n\n\n<ul class="wp-block-list">\n<li>Competitive salary and commission structure.</li>\n\n\n\n<li>Pension scheme and healthcare benefits.</li>\n\n\n\n<li>Ongoing training and development opportunities.</li>\n\n\n\n<li>Exciting work environment with opportunities for growth.</li>\n\n\n\n<li>Discounted tickets to Lancashire Cricket Club events.</li>\n</ul>\n\n\n\n<p>If you are a passionate sales professional with a strong interest in sports and partnerships, we would love to hear from you. Join us in driving the commercial success of Lancashire Cricket Club!</p>\n',
    link: 'https://cricket.jobs/job-listing/commercial-partnerships-sales-manager/',
