    jobTitle: 'Cricket Batting Coach Mentor',
    location: 'Location : Indonesia',
    address: 'Address : Bali, Indonesia',
    salary: 'Pay rate : AUD$ 1370per month',
    contractLength: 'Contract length : Full Time',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/04/1-32.jpeg',
    email: 'indigenouspathways@australianvolunteers.com',
    website: null,
    contactNumber: null,
    description:
      '\n<h2 class="wp-block-heading">About the assignment</h2>\n\n\n\n<p>Persatuan Cricket Indonesia (PCI) is looking for aCricket Batting Coach Mentor who canlive and volunteer for 6 months in Bali, Indonesia.</p>\n\n\n\n<ul class="wp-block-list">\n<li>This assignment seeks to optimize PCIs batting coaches planning skill in order to deliver specialized batting coach programs for the Mens and Womens Senior and U19 teams. The volunteer will also support the head coach in creating team strategies and individual performance plans (IPPs). This will involve collaboration with selectors, captains, and other coaching staff to identify and nurture emerging talent.</li>\n\n\n\n<li>By supporting the coaches, you will create professional players who are not only skillful but also nurtured in a positive and inclusive team culture that align with PCIs values of professionalism, sportsmanship, and unity.</li>\n\n\n\n<li>To be considered for this role, you must posses&nbsp;Level 1 Cricket Coaching Accreditation (Level 2 is a plus) or international equivalent. You also need to have proven experience in batting coaching at high-performance levels. Experience coaching in a multicultural or international environment will be a plus.</li>\n</ul>\n\n\n\n<p><strong>About the PartnerOrganisation</strong>&nbsp;</p>\n\n\n\n<p>Persatuan Cricket Indonesias (PCI) aim and purpose is to ensure that cricket becomes one of the better performing sports in Indonesia and to achieve a position as one of the top three popular sports in Indonesia. We are also attempting to expand the reach of Cricket to every region in Indonesia. Presently there are 18 provincial committees (out of 34 Provinces), ranging from Aceh, in the West to Papua in the East, with 3 association members; Bogor Cricket Association, Bali Cricket and Jakarta Cricket Association. Those associations are responsible for organizing competitions, introducing and socializing cricket in the society.</p>\n\n\n\n<p>PCI has succeeded with its programs and cricket has been growing fast in Indonesia. Many people have commenced playing cricket without regard to their age or gender. The sport is not limited to adults or expats; Men, Women and Children participate on an equal basis. PCI is continuing to develop its programs in order to be qualified to provide training, short courses and seminars for coaches, referees, and cricket supporting officials. The most important thing is becoming a national pride sport for Indonesia.</p>\n\n\n\n<p><strong>About the Australian Volunteers Program&nbsp;</strong>&nbsp;</p>\n\n\n\n<p>The Australian Volunteers Program is an Australian government-funded initiative. We support partner organisations across the Pacific, Asia, and Africa to achieve locally led change.&nbsp;&nbsp;</p>\n\n\n\n<p>Volunteers are supported throughout their&nbsp;<a href="https://www.australianvolunteers.com/volunteering/" target="_blank" rel="noreferrer noopener">volunteer journey</a>. We actively support and encourage people of all backgrounds and abilities to volunteer and aim to make the program as accessible and inclusive as possible. If you have accessibility requirements, please contact us to discuss how we can support you to volunteer with us &nbsp;<a href="mailto:inclusion@australianvolunteers.com" target="_blank" rel="noreferrer noopener">inclusion@australianvolunteers.com</a>.&nbsp;<a href="https://www.australianvolunteers.com/diversity-and-inclusion/" target="_blank" rel="noreferrer noopener">Learn more about our commitment to inclusion</a>.</p>\n\n\n\n<p><a href="https://australianvolunteers.file.force.com/sfc/dist/version/download/?oid=00D7F000001biaN&amp;ids=068Od00000LZZRy&amp;d=%2Fa%2FOd0000017uw9%2Fwvr2_lAssYBnqqE8X6_FNyho__CL39inDtNwjhQPG7A&amp;asPdf=false"><strong>Download assignment description</strong></a></p>\n\n\n\n<p>All applications must be submitted online through the Australian Volunteers Program website. If you have not already done so, you<br>will need to register on our website prior to applying. For more information about how to apply, please visit: <a href="https://www.australianvolunteers.com/volunteering/how-it-works">https://www.australianvolunteers.com/volunteering/how-it-works</a></p>\n\n\n\n<p></p>\n',
