      '\n<h2 class="wp-block-heading">About the assignment</h2>\n\n\n\n<p>Were looking for a Womens Cricket Coach who can live and volunteer in Indonesia for 6 months.</p>\n\n\n\n<ul class="wp-block-list">\n<li>As a volunteer, you will&nbsp;help to build the knowledge and skills of local Coaches helping ensure that quality Cricket players are identified and developed.</li>\n\n\n\n<li>To be considered for this role, youll need to be a Level 1 or higher coach or equivalent.</li>\n\n\n\n<li>You will be working alongside local coaches to build capacity and grow the sport of Cricket in Indonesia&nbsp;</li>\n</ul>\n\n\n\n<p><strong>About the PartnerOrganisation</strong>&nbsp;</p>\n\n\n\n<p>Persatuan Cricket Indonesia (PCI), based in Kupang, Indonesia, has the aim and purpose of ensuring cricket becomes one of the better-performing sports in the country and to achieve a position as one of the top three most popular sports. They are also attempting to expand the reach of Cricket to every region in Indonesia.</p>\n\n\n\n<p>PCI require an experienced Development Coach, to encourage greater participation, build on the skills of local coaches, and support the preparation of teams for larger events and tournaments.</p>\n\n\n\n<p><strong>About the Australian Volunteers Program&nbsp;</strong>&nbsp;</p>\n\n\n\n<p>The Australian Volunteers Program is an Australian government-funded initiative. We support partner organisations across the Pacific, Asia, and Africa to achieve locally led change.&nbsp;&nbsp;</p>\n\n\n\n<p>&nbsp;Volunteers are supported throughout their&nbsp;<a href="https://www.australianvolunteers.com/volunteering/" target="_blank" rel="noreferrer noopener">volunteer journey</a>. We actively support and encourage people of all backgrounds and abilities to volunteer and aim to make the program as accessible and inclusive as possible. If you have accessibility requirements, please contact us to discuss how we can support you to volunteer with us &nbsp;<a href="mailto:inclusion@australianvolunteers.com" target="_blank" rel="noreferrer noopener">inclusion@australianvolunteers.com</a>.&nbsp;<a href="https://www.australianvolunteers.com/diversity-and-inclusion/" target="_blank" rel="noreferrer noopener">Learn more about our commitment to inclusion</a>.</p>\n\n\n\n<p><a href="https://australianvolunteers.file.force.com/sfc/dist/version/download/?oid=00D7F000001biaN&amp;ids=068Od00000GTrXq&amp;d=%2Fa%2FOd000000lCLt%2FWTSF5y__aDS1zhOus9UvW9tJIn04v6lc4QPat1mA9IA&amp;asPdf=false"><strong>Download assignment description</strong></a></p>\n\n\n\n<p>All applications must be submitted online through the Australian Volunteers Program website. If you have not already done so, you will<br>need to register on our website prior to applying. For more information about how to apply, please visit:<br><a href="https://www.australianvolunteers.com/volunteering/how-it-works">https://www.australianvolunteers.com/volunteering/how-it-works</a></p>\n\n\n\n<p></p>\n',
