    description:
      '\n<p><strong>Curriculum Delivery</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Plan, prepare, and deliver engaging PE lessons aligned with the school curriculum -CBSE</li>\n\n\n\n<li>Teach students various physical activities, sports, and fitness concepts.</li>\n\n\n\n<li>Design age-appropriate training programs that promote physical health and mental well-being.</li>\n</ul>\n\n\n\n<p><strong>2. Student Development</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Encourage students to participate actively and develop physical competence and fitness.</li>\n\n\n\n<li>Assess students physical abilities and provide constructive feedback.</li>\n\n\n\n<li>Foster team spirit, discipline, sportsmanship, and healthy competition among students.</li>\n</ul>\n\n\n\n<p><strong>3. Sports Events &amp; Competitions</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Organize intra-school and inter-school sports events, competitions, and annual sports day.</li>\n\n\n\n<li>Train and coach school teams for various sports tournaments.</li>\n\n\n\n<li>Accompany students during external competitions and ensure their safety and discipline.</li>\n</ul>\n\n\n\n<p><strong>4. Safety &amp; Equipment Management</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Ensure the physical safety of students during all activities.</li>\n\n\n\n<li>Maintain and inspect sports equipment and playing areas regularly.</li>\n\n\n\n<li>Report any damage or required replacements to the admin or HOD.</li>\n</ul>\n\n\n\n<p><strong>5. Student Health &amp; Fitness</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Promote awareness of a healthy lifestyle, nutrition, and physical fitness.</li>\n\n\n\n<li>Support students with special physical needs or those requiring individualized fitness programs.</li>\n\n\n\n<li>Keep records of student performance, health notes, and attendance in PE classes.</li>\n</ul>\n\n\n\n<p><strong>6. Collaboration &amp; Communication</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Coordinate with school staff and parents regarding student development and participation.</li>\n\n\n\n<li>Attend staff meetings, parent-teacher meetings, and school development programs.</li>\n\n\n\n<li>Maintain clear communication with administration for scheduling and resource planning.</li>\n</ul>\n\n\n\n<p><strong>7. Professional Development</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Stay updated with the latest PE teaching methodologies, rules of sports, and health education practices.</li>\n\n\n\n<li>Participate in workshops, seminars, and school training programs.</li>\n</ul>\n\n\n\n<p>Schedule:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Day shift</li>\n</ul>\n',
