      '\n<p>Qualification HSC pass (Relax able for deserving Candidates), certification in Sports coaching from SAI NIS (diploma or six-week course) or equivalent certification in coaching from sports associations/federations with 3 years experience as a coach at School or College Level and experience of having played at the District, State &amp; National Level .</p>\n\n\n\n<p><strong>Foundation</strong></p>\n\n\n\n<p>SVKM  is committed to the cause of providing high quality education at various levels and has over the past 78 years grown into an educational foundation. The group is focussed at developing global thinking consistent with national interest amongst youngster. Through over 40 prestigious institutes, SVKM is inculcating values, professionalism, social sensitivity and dynamic entrepreneurship in more than 50,000 students throughout India.</p>\n\n\n\n<p><strong>Our Schools</strong></p>\n\n\n\n<p>Children adapt to environments naturally, unafraid, unbiased, unquestioning, unlimited. Their minds are curious and sharp. Seeking a creative, positive and open ambiance to learn and grow in. At MRPS CBSE, Residential School, Shirpur, we aim to provide each child with a nurturing, open and stress free environment that enables excellence outside of conventional formats. We welcome young children, as early as 6 years of age in our sprawling XX acre campus, designed with ample open spaces within and outside the academic corridors. Where equal thought has been put in defining learning spaces in all areas, with special attention to individual, social, emotional, intellectual, physical, cultural and spiritual development. A co-educational school, MRPS CBSE, Shirpur, launched in April 20XX, offers admissions to students from Nursery to Grade 9th. With well equipped residential facilities for boys and girls from Grade 1 onwards.</p>\n',
