    jobTitle: 'Cricket Coach  Tida Sports',
    location: 'Location : India',
    address: 'Address : Ludhiana, Punjab, India',
    salary: 'Pay rate : 8,000.00 - 15,000.00 per month',
    contractLength: 'Contract length : Full Time',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/04/1-15.png',
    email: null,
    website: null,
    contactNumber: null,
    description:
      '\n<h2 class="wp-block-heading">Full Job Description</h2>\n\n\n\n<p>TIDA Sports is a leading sports academy dedicated to nurturing young athletic talent and fostering a love for sports. We provide comprehensive coaching and training programs in various sports, including cricket. Our mission is to empower athletes to reach their full potential through expert guidance and a supportive environment.</p>\n\n\n\n<p><strong>Position Summary:</strong></p>\n\n\n\n<p>We are seeking a passionate and experienced Cricket Coach to join our team at TIDA Sports. The ideal candidate will have a strong understanding of cricket fundamentals, excellent coaching skills, and a commitment to developing young athletes.</p>\n\n\n\n<p><strong>Responsibilities:</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li><strong>Coaching:</strong></li>\n\n\n\n<li>Conduct cricket training sessions for various age groups, including children and teenagers.</li>\n\n\n\n<li>Teach fundamental cricket skills, such as batting, bowling, fielding, and wicket-keeping.</li>\n\n\n\n<li>Develop and implement personalized training plans for individual athletes based on their abilities and goals.</li>\n\n\n\n<li>Provide feedback and guidance to athletes to help them improve their performance.</li>\n\n\n\n<li><strong>Team Development:</strong></li>\n\n\n\n<li>Coach cricket teams for competitions and tournaments.</li>\n\n\n\n<li>Develop strategies and game plans for matches.</li>\n\n\n\n<li>Motivate and inspire athletes to achieve their best.</li>\n\n\n\n<li><strong>Player Development:</strong></li>\n\n\n\n<li>Identify and nurture talented young cricket players.</li>\n\n\n\n<li>Assist in the selection and development of players for representative teams.</li>\n\n\n\n<li>Provide guidance on nutrition, fitness, and injury prevention.</li>\n\n\n\n<li><strong>Program Development:</strong></li>\n\n\n\n<li>Contribute to the development of new cricket programs and initiatives.</li>\n\n\n\n<li>Assist in organizing cricket camps and clinics.</li>\n\n\n\n<li><strong>Community Engagement:</strong></li>\n\n\n\n<li>Represent TIDA Sports at community events and tournaments.</li>\n\n\n\n<li>Promote the academy and its programs to potential students and their families.</li>\n</ul>\n\n\n\n<p><strong>Qualifications:</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Proven experience as a cricket coach, preferably with a focus on youth development.</li>\n\n\n\n<li>Strong knowledge and understanding of cricket rules, techniques, and strategies.</li>\n\n\n\n<li>Excellent coaching skills and the ability to communicate effectively with athletes of all ages.</li>\n\n\n\n<li>Passion for cricket and a commitment to developing young talent.</li>\n\n\n\n<li>Positive and enthusiastic attitude.</li>\n</ul>\n',
