    description:
      '\n<p>INTRODUCTION</p>\n\n\n\n<p>Gamepoint is an award-winning sports company operating a chain of world class multi-sport centres with sports such as Badminton, Squash, Table Tennis, Basketball, Football, Swimming and Pickleball. Gamepoint offers services such as coaching programs, membership, book n play and conduction of sports events to individuals and institutional customers. Successful serial entrepreneurs including IIM alumnus have setup Gamepoint with the vision to be the most trusted and preferred sports destination in India. More details are available at www.gamepointindia.com</p>\n\n\n\n<p>At Gamepoint, we believe that sports has the power to transform individuals and communities. We seek enthusiastic individuals who share our passion for sports and are eager to contribute to our vision.</p>\n\n\n\n<p>RESPONSIBILITIES</p>\n\n\n\n<ul class="wp-block-list">\n<li>Conduct structured Cricket coaching sessions for children and adults</li>\n\n\n\n<li>Train players across beginner, intermediate, and advanced levels</li>\n\n\n\n<li>Plan and implement training programs focusing on technical, tactical, and physical development</li>\n\n\n\n<li>Prepare and guide players for tournaments and competitive matches</li>\n\n\n\n<li>Monitor and assess player progress on a regular basis</li>\n\n\n\n<li>Ensure a safe, engaging, and disciplined training environment</li>\n</ul>\n\n\n\n<p>KEY COMPETENCIES</p>\n\n\n\n<ul class="wp-block-list">\n<li>Should have played Cricket at a competitive level for at least 5 years</li>\n\n\n\n<li>Minimum 2 years of experience in conducting Cricket coaching</li>\n\n\n\n<li>Relevant coaching certifications (BCCI/ ICC or equivalent) are preferred</li>\n\n\n\n<li>Strong communication and interpersonal skills</li>\n\n\n\n<li>Ability to inspire, motivate and develop players across age groups</li>\n</ul>\n\n\n\n<p>Job Type: Full-time</p>\n\n\n\n<p>Pay: 25,000.00  33,488.29 per month</p>\n\n\n\n<p>Benefits:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Health insurance</li>\n\n\n\n<li>Provident Fund</li>\n</ul>\n\n\n\n<p>Schedule:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Day shift</li>\n\n\n\n<li>Rotational shift</li>\n</ul>\n\n\n\n<p>Work Location: In person</p>\n',
