    description:
      '\n<p><strong>Role Description</strong></p>\n\n\n\n<p>This is a full-time on-site role located in Bengaluru for a Cricket Writer at Sportscafe.in. As a Cricket Writer, you will write high-quality articles on international and domestic cricket for our platform. You will write match previews, analyses, features, and opinion pieces. You will also be required to research trends and news in cricket, stay up-to-date with the latest happenings, and provide innovative insights and perspectives to engage readers. You will work closely with the editorial team to ensure consistent style and tone across all content.</p>\n\n\n\n<p><strong>Qualifications</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>A passion for cricket and a strong understanding of its rules and nuances</li>\n\n\n\n<li>Excellent written communication skills, with strong attention to grammar, punctuation, and style</li>\n\n\n\n<li>Demonstrated experience in writing content related to cricket, with a portfolio of published articles</li>\n\n\n\n<li>Familiarity with SEO best practices and writing for the web</li>\n\n\n\n<li>A Bachelors degree in journalism, English, communications, or a related field (or equivalent experience)</li>\n\n\n\n<li>Ability to work effectively in a team environment, take constructive feedback, and meet deadlines</li>\n</ul>\n\n\n\n<p>Ability to commute/relocate:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Bangalore, Karnataka: Reliably commute or planning to relocate before starting work (Required)</li>\n</ul>\n\n\n\n<p>Application Question(s):</p>\n\n\n\n<ul class="wp-block-list">\n<li>Do you follow and have in-depth knowledge of Cricket?</li>\n</ul>\n',
