      '\n<p><strong>Job Summary</strong></p>\n\n\n\n<p><strong>Looking for an experienced and qualified PE teacher with Cricket Speciality</strong></p>\n\n\n\n<p><strong>Role include:</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Planning and execution of Cricket curriculum at school</li>\n\n\n\n<li>Executing Sport Specific coaching to school students</li>\n\n\n\n<li>Motivating all students to participate in physical activity.</li>\n\n\n\n<li>Monitoring and enhancing performance by providing training, encouragement and constructive feedback</li>\n\n\n\n<li>Identifying strengths and weaknesses of students</li>\n\n\n\n<li>Awareness about health issues and encouraging for a healthy life</li>\n\n\n\n<li>Developing various training programmes related to table tennis for students</li>\n\n\n\n<li>Teaching relevant sports specific skills, tactics and techniques</li>\n\n\n\n<li>Inducing teamwork and team building among the students</li>\n\n\n\n<li>Maintaining equipment and equipment inventory</li>\n\n\n\n<li>Documentation of all PE and Sports related activities</li>\n</ul>\n\n\n\n<p>Schedule:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Day shift</li>\n\n\n\n<li>Evening shift</li>\n</ul>\n',
