    jobTitle: 'Urgent Hiring For Cricket Coach  Vibgyor High Gurugram',
    location: 'Location : India',
    address: 'Address : Gurugram, Haryana, India',
    salary: 'Salary : Depending on experience',
    contractLength: 'Contract length : Full Time, Permanent',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/04/1-24.jpeg',
    email: 'mohit.bansal@vgos.org',
    website: null,
    contactNumber: null,
    description:
      '\n<p><strong>Job Description</strong></p>\n\n\n\n<p><strong>Primary Purpose:</strong>&nbsp;The Coach is responsible for facilitating academic and sports atmosphere where students have opportunities to explore their mental and physical talents contributing to their overall development by helping them to lead an active and healthy lives.</p>\n\n\n\n<p><strong>Key Accountabilities/Activities:</strong></p>\n\n\n\n<p><strong>Primary Responsibility:</strong></p>\n\n\n\n<p><strong>Student Administration</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>The Coach should ensure that the student growth and achievement is continuous and appropriate for a specific age group and develop desirable personal and social attributes: the concept of fair play, the acceptance of success and failure, and the ability to co-operate in group situations. The following are the areas to be focused on:</li>\n\n\n\n<li>Should be approachable and treat students fairly and objectively.</li>\n\n\n\n<li>Inculcate discipline in and out of the training sessions.</li>\n\n\n\n<li>Treat children with dignity and respect.</li>\n\n\n\n<li>Promote maximum student participation and interest and assist students in analyzing and improving fitness.</li>\n\n\n\n<li>Monitor student progress constantly and adjust pace accordingly.</li>\n\n\n\n<li>Conduct checks for improperly dressed students during SPA.</li>\n\n\n\n<li>Keep a check on basic hygiene, is aware of the previous history of each child and attends to health matters seriously and diligently .</li>\n\n\n\n<li>Ensures the safety of players through careful planning of practice sessions, use of equipment and games.</li>\n\n\n\n<li>Ensures the development of students at a level of skill and competence necessary for Competition at the school level.</li>\n\n\n\n<li>Coach should familiarize (himself/herself) with the SPA Manual, the Teachers Manual and the Parent Manual,(which includes the details in regards to SPA, Special Education and QAG) thoroughly to understand the core functions and style of working of the organisation.</li>\n</ul>\n\n\n\n<p><strong>Special Events</strong></p>\n\n\n\n<p>To lead the development of a high performance programme that aims to achieve the highest result possible in inter school competitions. To search for, select, develop and maximise the potential of the School Team and to increase the number of players reaching the very highest levels of state and national performance</p>\n\n\n\n<ul class="wp-block-list">\n<li>Be actively involved in the planning, organising and coordination of the Annual Sports Day.</li>\n\n\n\n<li>Participate in organizing and implementing the special events of the school.</li>\n\n\n\n<li>Assist in public relations events and other special events sponsored by school.</li>\n\n\n\n<li>To notify the SPA Coordinator in advance of any tournaments, practice session etc.</li>\n\n\n\n<li>To be responsible for the conduct and sportsmanship of his/her students during practice, at competitions and in public when representing the school.</li>\n</ul>\n\n\n\n<p><strong>SPA Functioning</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>To implement SPA training sessions, training rules and a system of play and scouting program suitable to the sport as per the SPA curriculum.</li>\n\n\n\n<li>To assume responsibility for equipment and materials issued and ensure that all are returned in good condition.</li>\n\n\n\n<li>For all daily practice sessions, the coach should arrive at the court dressed for practice at least 5 minutes before the beginning of practice. He, or she, should review the days scheduled practice. The coach should be sure that the necessary materials are available.</li>\n\n\n\n<li>To provide constant supervision of the players at practice facilities and remain until all athletes have dispersed.</li>\n\n\n\n<li>To design and implement high quality periodised training and competition programmes for students.</li>\n</ul>\n\n\n\n<p><strong>Secondary Responsibility:</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Coaches will remain present at PTMs. They will maintain and keep individual and team assessment records and share them with players and parents at the PTM.</li>\n\n\n\n<li>Coaches will assemble a brief scouting report for every game, especially playoff games. Review the report with team at practice sessions before each game.</li>\n\n\n\n<li>To report injuries of participants to the SPA Coordinator.</li>\n\n\n\n<li>At the conclusion of the month submit monthly assessment reports and a summary of monthly activities report to the SPA coordinator</li>\n\n\n\n<li>Committed to continuous personal and organisational improvement.</li>\n</ul>\n\n\n\n<p>The Coach should follow professional etiquette consistent with school systems and policies while working with students, parents, and colleagues.</p>\n\n\n\n<p><strong>Maintenance of Records</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Be aware in terms of the process &amp; maintenance of Records as per guidelines mentioned in the SPA Manual.</li>\n\n\n\n<li>Compile and maintain appropriate, accurate, complete, and correct records and provide relevant, accurate and up-to date information for school systems.</li>\n\n\n\n<li>Maintain confidentiality of students records.</li>\n\n\n\n<li>Maintain Records of :</li>\n\n\n\n<li>Attendance Register</li>\n\n\n\n<li>Class list</li>\n</ul>\n\n\n\n<p><strong>Assist the Coordinator to update records in regards to events such as</strong></p>\n\n\n\n<p>a) Sports Day</p>\n\n\n\n<p>b) PTMs</p>\n\n\n\n<p>c) Camps, Tournaments and Competitions</p>\n\n\n\n<p>d) School Team Records</p>\n\n\n\n<p>e) Monthly Assessment Records</p>\n\n\n\n<p>f) Equipment Records</p>\n\n\n\n<p>g) Student Participation and Achievement Records</p>\n\n\n\n<p>h) Training Session Sheets</p>\n\n\n\n<p>Interested candidate can share resume at&nbsp;<strong>mohit.bansal@vgos.org</strong></p>\n',
