    description:
      '\n<h2 class="wp-block-heading">Job Description</h2>\n\n\n\n<p>Vibgyor Roots Rise is looking for COACH  CRICKET to join our dynamic team and embark on a rewarding career journey</p>\n\n\n\n<p>Develop and implement comprehensive training programs tailored to the needs and goals of individual athletes or teams</p>\n\n\n\n<p>Provide instruction and demonstrate proper techniques for skills development, conditioning, and game strategy</p>\n\n\n\n<p>Motivate and inspire athletes to perform at their best through positive reinforcement, encouragement, and constructive feedback</p>\n\n\n\n<p>Evaluate athletes progress and performance during training sessions and competitions, and adjust training plans as needed</p>\n\n\n\n<p>Collaborate with other coaches, athletic trainers, and support staff to ensure a holistic approach to athlete development</p>\n\n\n\n<p>Foster a positive and inclusive team culture that promotes sportsmanship, teamwork, and respect for others</p>\n\n\n\n<p>Communicate effectively with athletes, parents, and stakeholders to provide updates on training schedules, performance goals, and other relevant information</p>\n\n\n\n<p>Maintain accurate records of attendance, performance evaluations, and other administrative tasks related to coaching responsibilities</p>\n\n\n\n<p>Stay current with trends, best practices, and advancements in coaching techniques and sports science</p>\n\n\n\n<p>Uphold ethical standards and promote the health, safety, and well-being of athletes at all times</p>\n\n\n\n<h2 class="wp-block-heading">About company</h2>\n\n\n\n<p>1st-century education should prepare students in a way that they have the requisite tools to thrive and navigate through the dynamic world of today. With this vision in mind, VIBGYOR Schools were founded in 2004. With 38 schools in 14 major cities, reaching out to more than 50,000 students across India, quality-driven systemic processes, and a dedicated and proficient team of teaching and school administration professionals, we are an institution that fosters young talent to make successful leaders of tomorrow. Our education system emphasizes Experiential Learning through a Holistic learning program within a dynamic background that nurtures students into progressive individuals armed with 21st-century skills.</p>\n',
