    description:
      '\n<h2 class="wp-block-heading">Job description</h2>\n\n\n\n<p>CANDIIDUS International School is looking for Cricket Coach to join our dynamic team and embark on a rewarding career journey</p>\n\n\n\n<ul class="wp-block-list">\n<li>you will be responsible for developing and enhancing the cricket skills of players, fostering a positive team environment, and promoting a culture of sportsmanship and excellence</li>\n\n\n\n<li>This role involves coaching players at various skill levels, creating training programs, and coordinating with team management</li>\n\n\n\n<li>Responsibilities:Player Development:Assess the skill levels of players and design individualized development plans</li>\n\n\n\n<li>Provide hands-on coaching to enhance batting, bowling, fielding, and overall cricketing skills</li>\n\n\n\n<li>Team Training:Plan and conduct team training sessions focusing on specific skills, tactics, and game strategies</li>\n\n\n\n<li>Implement effective drills and exercises to improve overall team performance</li>\n\n\n\n<li>Game Strategy:Develop and implement game strategies in collaboration with the team management</li>\n\n\n\n<li>Analyze opponents and provide insights to players on opposition strengths and weaknesses</li>\n\n\n\n<li>Fitness and Conditioning:Collaborate with fitness trainers to design and implement fitness programs</li>\n\n\n\n<li>Monitor players physical conditioning and provide recommendations for improvement</li>\n\n\n\n<li>Mentorship:Act as a mentor to players, providing guidance on mental toughness, leadership, and teamwork</li>\n\n\n\n<li>Foster a positive team culture that values sportsmanship and camaraderie</li>\n\n\n\n<li>Match Preparation:Prepare players mentally and physically for matches</li>\n\n\n\n<li>Conduct pre-match briefings and strategy sessions to ensure players are well-prepared</li>\n\n\n\n<li>Skill Analysis:Use video analysis and other tools to assess player performance and identify areas for improvement</li>\n\n\n\n<li>Provide constructive feedback and work on rectifying technical flaws</li>\n\n\n\n<li>Player Recruitment:Collaborate with team management in identifying and recruiting new talent</li>\n\n\n\n<li>Assist in scouting and selecting players for the team</li>\n\n\n\n<li>Community Engagement:Engage with the cricket community, including local clubs and schools</li>\n\n\n\n<li>Conduct cricket clinics and workshops to promote the sport</li>\n</ul>\n\n\n\n<h2 class="wp-block-heading">About company</h2>\n\n\n\n<p>Candiidus International School places students at the centre of learning, creating opportunities to be reflective, adaptive, and inclusive that leads them to become socially responsible and dedicated voices for positive change.</p>\n',
