    description:
      '\n<p><strong>Job Summary:</strong></p>\n\n\n\n<p>We are seeking a dedicated and experienced Cricket Coach to lead and develop our cricket team. The ideal candidate will be responsible for designing training programs, improving player performance, instilling sportsmanship, and preparing the team for competitive matches.</p>\n\n\n\n<p><strong>Key Responsibilities:</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Plan, develop, and implement effective training sessions and practice schedules.</li>\n\n\n\n<li>Teach cricket techniques, strategies, and game rules to players.</li>\n\n\n\n<li>Analyze individual and team performance, and provide constructive feedback.</li>\n\n\n\n<li>Promote teamwork, discipline, and a positive sporting attitude.</li>\n\n\n\n<li>Scout and assess player potential for selection and development.</li>\n\n\n\n<li>Prepare the team physically and mentally for tournaments and matches.</li>\n\n\n\n<li>Ensure the safety and well-being of all players during training and games.</li>\n\n\n\n<li>Maintain equipment and ensure proper use of training facilities.</li>\n\n\n\n<li>Communicate with parents, staff, and management as needed.</li>\n\n\n\n<li>Stay updated with modern coaching techniques and cricket developments.</li>\n</ul>\n',
