    description:
      '\n<p><strong>*About Us:*</strong></p>\n\n\n\n<p>We are an online coaching platform dedicated to helping cricketers improve their skills through expert-led courses. With a growing <strong>*YouTube channel (75,000+ subscribers)* </strong>and a strong presence in the cricket coaching community, we are looking for a passionate<strong> *Sales Intern* </strong>to help us expand our reach and drive course enrollments.</p>\n\n\n\n<p><strong> *Role &amp; Responsibilities:*</strong></p>\n\n\n\n<p> *Lead Generation:* Identify and connect with aspiring cricketers, parents, and coaches who may be interested in our courses.</p>\n\n\n\n<p><strong> *Sales Outreach:*</strong> Engage with potential customers via calls, WhatsApp, emails, and social media to understand their needs and pitch our courses.</p>\n\n\n\n<p><strong> *Follow-ups:*</strong> Maintain regular communication with interested leads and guide them toward enrollment.</p>\n\n\n\n<p><strong> *Customer Support:*</strong> Answer inquiries regarding course details, pricing, and benefits.</p>\n\n\n\n<p><strong> *Sales Target:* </strong>Work towards achieving weekly and monthly enrollment goals.</p>\n\n\n\n<p><strong>*Who Can Apply?*</strong></p>\n\n\n\n<p> Passionate about <strong>*sales &amp; cricket* </strong>(having a cricket background is a plus).</p>\n\n\n\n<p> Strong <strong>*communication &amp; persuasion skills*</strong> in Hindi and English.</p>\n\n\n\n<p> Comfortable using <strong>*WhatsApp, Instagram, and other social media*</strong> for sales outreach.</p>\n\n\n\n<p> Self-motivated and eager to learn about online sales and marketing.</p>\n\n\n\n<p> Freshers and students are welcome to apply.</p>\n\n\n\n<p><strong>*Perks &amp; Benefits:*</strong></p>\n\n\n\n<p> Performance-based *incentives* in addition to the stipend.</p>\n\n\n\n<p> Certificate &amp; Letter of Recommendation.</p>\n\n\n\n<p> Hands-on experience in<strong> *sports sales &amp; marketing*.</strong></p>\n\n\n\n<p> Opportunity for a *full-time role* based on performance.</p>\n\n\n\n<p> <strong>*Ready to kickstart your sales career?* </strong>Apply now and help us take cricket coaching to the next level!</p>\n',
