    description:
      '\n<p>1st Responsibility: <strong>I ensure a WOW Customer Experience</strong></p>\n\n\n\n<p><strong>I greet, smile, and engage with customers to understand their needs</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>I am equipped to interact and engage with my customers</li>\n</ul>\n\n\n\n<p><strong>I promote sports</strong><strong>by engaging customers through sports communities</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>I practice my sport and inspire customers to join sports communities.</li>\n\n\n\n<li>I stay informed and encourage customers to participate in Decathlon events or local sports activities related to their interests.</li>\n</ul>\n\n\n\n<p><strong>I ensure my customer is Satisfied or Satisfied</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>I ensure my customer is Satisfied or Satisfied</li>\n\n\n\n<li>I propose the right products &amp; services depending on the needs of my customers</li>\n\n\n\n<li>I use all our channels (in-store, .in, App) to satisfy the customers needs</li>\n\n\n\n<li>I propose the relevant services to the customer (buyback, repair &amp; maintenance, personalisation).</li>\n\n\n\n<li>I ensure a smooth checkout experience</li>\n\n\n\n<li>My customer is at the centre of my activity. I make myself available whatever I am doing.</li>\n\n\n\n<li>I suggest complementary products to enhance the customer sports practice experience</li>\n\n\n\n<li>I ensure product availability by doing reliable, up-to-date and responsible inventory to support its omnichannel availability.</li>\n\n\n\n<li>I ensure the signages are updated for the customers (Self-informed best choice)</li>\n\n\n\n<li>I am accountable for the delivery</li>\n</ul>\n\n\n\n<p>2nd Responsibility: <strong>I guarantee WOW displayed layout</strong></p>\n\n\n\n<p><strong>The layout</strong> <strong>is the best in class for my customer</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>My layout is safe for me, my team, and my customers.</li>\n\n\n\n<li>My Layout is welcoming, seductive and engaging.</li>\n\n\n\n<li>I ensure my customers can test all our solutions</li>\n</ul>\n\n\n\n<p><strong>I know and highlight the best omnichannel offer to my customers, both offline and online</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>I know the complete offer in my store and online</li>\n\n\n\n<li>I ensure My customer will find the entire range of products for their sports practice  either in-store or online</li>\n\n\n\n<li>I am product trained by a validated product trainer.</li>\n\n\n\n<li>I suggest the best possible solution to my customer as per their need</li>\n\n\n\n<li>I ensure all the necessary services for my sport are available to my customer</li>\n\n\n\n<li>I highlight our Circular offer and relevant services</li>\n</ul>\n\n\n\n<p>Are you a person who would love to work with people and enjoy collective decision</p>\n\n\n\n<p>Passionate about providing exceptional customer experiences</p>\n\n\n\n<p>You are a passionate sports lover</p>\n\n\n\n<p>Keen to adapt/learn to Omnichannel expertise to blend online and offline customer experiences through digital tools and data analysis to optimize business performance.</p>\n\n\n\n<p><strong>Do check our Video on the role and responsibility of an Omni Sports Advisor  <a href="https://www.youtube.com/watch?v=EHQ8mJ6FBVI">https://www.youtube.com/watch?v=EHQ8mJ6FBVI</a></strong></p>\n',
    link: 'https://cricket.jobs/job-listing/omni-sport-leadercricketbadmintontennisfootball/',
