    description:
      '\n<p><strong>The ideal candidate:</strong></p>\n\n\n\n<p>Are you a Cricket fanatic who possesses a keen interest in Cricket and everything associated with it? We at <strong>OneCricket </strong>are looking for a Cricket Commentator to undertake all the operations with respect to cricket commentary for our websites and applications.</p>\n\n\n\n<p><strong>Work expectations:</strong></p>\n\n\n\n<p> Updating ball-to-ball commentary on our portal with maximum accuracy and agility.</p>\n\n\n\n<p> Updating previews and reviews with accuracy on our portal.</p>\n\n\n\n<p> Work closely with relevant Operations colleagues.</p>\n\n\n\n<p><strong>The Team:</strong></p>\n\n\n\n<p>At OneCricket, we are constantly iterating, solving problem statements and working together to connect homogeneous minds from all over the world to ultimately build a constructive workforce on this planet. Thats why its our liability that our workforce reflects the culture of the people we serve. Hiring people with different backgrounds and points of view helps us make better decisions, build better products and create better experiences for everyone.</p>\n\n\n\n<p><strong>How will you grow?</strong></p>\n\n\n\n<p>At OneCricket, we have invested a great deal to create a rich environment in which our professionals can grow. We want all our people to develop in their own way, playing with their own strengths as they enhance their leadership skills. And, as a part of our efforts, we provide our professionals with a variety of learning and networking opportunities including exposure to leaders, clients, and challenging projects to help accelerate their careers along the way. No two people learn in exactly the same way. So, we provide a range of resources to ensure the personal growth of the professional is not compromised at any instance.</p>\n\n\n\n<p></p>\n',
