    description:
      '\n<p><strong>Who are we?</strong></p>\n\n\n\n<p>Cricket Scotland is the national governing body for the sport of cricket in Scotland. It is responsible for organising and developing the game at all levels, from grassroots participation to elite international competitions. The organisations primary goal is to promote and enhance the growth of cricket in Scotland, both in terms of player development and the overall popularity of the sport. Cricket Scotlands new four-year strategy focuses on this goal through a desire to unite people and communities through cricket.</p>\n\n\n\n<p>Cricket has a long history in Scotland, with records dating back to the 18th century. Scotland became an associate member of the International Cricket Council (ICC) in 1994.</p>\n\n\n\n<p>The Scotland mens and womens national cricket teams compete in various international tournaments and both teams have achieved recent notable successes, with memorable victories against higher-ranked teams and impressive performances in global competitions. These achievements have helped raise the profile of cricket in Scotland and inspire more young players to pursue the sport. The senior teams successes have been backed up by the mens and womens U19 teams consistently reaching their respective ICC World Cups.</p>\n\n\n\n<p>In addition to the national teams, Cricket Scotland is actively involved in developing cricket at the grassroots level. The organisation supports and organises various leagues, tournaments, and coaching programmes throughout the country. These initiatives aim to increase participation, identify talented players, and provide them with the necessary training and resources to progress in the sport.</p>\n\n\n\n<p><strong>Whats happening in Cricket?</strong></p>\n\n\n\n<p>In addition to the significant high-performance success of our national teams, including Scotlands inclusion at our first ever ICC Womens T20 World Cup in 2024, Cricket Scotland is currently reviewing and updating our governance arrangements to ensure we build a highperforming governing body of sport for the future. To achieve this, we recently launched our 2024-2028 strategy  Uniting People and Communities through Cricket  which clarifies our vision and the actions we will take to achieve our strategic objectives.</p>\n\n\n\n<p>Our strategy sets out a definitive vision and pathway for cricket in Scotland, with a particular focus on:</p>\n\n\n\n<p> Inspiring women and girls to play cricket</p>\n\n\n\n<p> Embedding a culture of equality</p>\n\n\n\n<p> Developing sustainable success on the pitch</p>\n\n\n\n<p> Cricket Scotland becoming a trusted and effective governing body</p>\n\n\n\n<p><strong>Why join us?</strong></p>\n\n\n\n<p>This is an exciting time for Cricket Scotland as we look to move forward as an organisation to deliver our vision for the sport. Scotland will also cohost to the ICC Mens T20 World Cup in 2030 with England and Ireland.</p>\n\n\n\n<p><strong>What is the role of the Domestic Cricket Committee?</strong></p>\n\n\n\n<p>The Domestic Cricket Committee (DCC) is one of the standing committees of Cricket Scotland Council Limited (CSCL) Board. Its primary purpose is to ensure that the organisation has a long-term domestic cricket plan which is consistent with the organisational strategy and to ensure that effective monitoring and reporting is in place.</p>\n\n\n\n<p>The duties and responsibilities of the DCC include (but are not limited to):</p>\n\n\n\n<p> advising the CSCL Board on the strategic development of the domestic game;</p>\n\n\n\n<p> providing recommendations to better align domestic cricket covering, for example, formats, rules, competitions and disciplinary process;</p>\n\n\n\n<p> recommending new or revised tournaments, formats and leagues;</p>\n\n\n\n<p> overseeing the governance of Cricket Scotland competition forums and management groups;</p>\n\n\n\n<p> growing the profile of the domestic game including streaming and social media.</p>\n\n\n\n<p>Role Description</p>\n\n\n\n<p>Offer Sound Advice and Guidance on Cricket Related Matters</p>\n\n\n\n<p> Provide insight and expertise on issues affecting regional associations, leagues, clubs and players at all levels.  Support the delivery of a domestic cricket calendar that ensures fairness, inclusivity, and player welfare.</p>\n\n\n\n<p> Monitor and review the domestic games performance, recommending improvements and innovations where necessary.</p>\n\n\n\n<p> Contribute to the development and implementation of policies for domestic cricket competitions, including league structures, cup competitions, and grassroots development.</p>\n\n\n\n<p> Collaborate with stakeholders, including regional associations, clubs, and match officials, to promote best practices and maintain high standards in domestic cricket.</p>\n\n\n\n<p> Attend and contribute to regular committee meetings and working groups, actively participating in decision-making processes.</p>\n\n\n\n<p><strong>Drive Cultural Change</strong></p>\n\n\n\n<p> Play a central role on the Committee focused on driving cultural change within Cricket Scotland and across Scottish cricket.</p>\n\n\n\n<p> Uphold the values of Cricket Scotland and act in the best interests of the domestic game at all times.</p>\n\n\n\n<p><strong>Strategic Planning and Judgement</strong></p>\n\n\n\n<p> Bring outside knowledge, experience, and innovative perspectives to the organisation by acting as a project sponsor for various initiatives.</p>\n\n\n\n<p> Ensure the alignment of domestic cricket activities with Cricket Scotlands broader strategic objectives.</p>\n\n\n\n<p>Relationships and Representation</p>\n\n\n\n<p> Develop relationships with relevant partners and stakeholders in conjunction with the Chair of the Committee, as required.</p>\n\n\n\n<p> Attend relevant Cricket Scotland meetings and events, contributing to the organisations profile and impact</p>\n\n\n\n<p><strong>Other</strong></p>\n\n\n\n<p> Travel across Scotland and overnight accommodation may be required as part of this role.</p>\n',
