    description:
      '\n<p><strong>The Opportunity</strong></p>\n\n\n\n<p>We are excited to be looking for a Guest Experience Assistant&nbsp;to join the Guest experience team&nbsp;at Emirates Old Trafford.</p>\n\n\n\n<p><strong>About Us</strong></p>\n\n\n\n<p>Emirates Old Trafford, home of Lancashire Cricket, is an award-winning world-class stadium and venue. It is recognised as one of the UKs most prestigious venues for sport, business, leisure and entertainment with a capacity of up to 50,000 people for concerts; 26,000 for international cricket matches; meetings and events spaces for up to 2,000 delegates; a four-star&nbsp;hotel with 250-bedroom Hilton Garden Inn hotel, which has 135 pitch facing rooms, and an on-site Caff Nero.</p>\n\n\n\n<p>The venue has played host to some of the worlds biggest sporting and music names, take a look at all the exciting fixtures and events weve got coming up&nbsp;<a href="https://emiratesoldtrafford.lancashirecricket.co.uk/cricket-concerts/">here</a>&nbsp;Its a unique place to build a career where no two days are ever the same!</p>\n\n\n\n<p>We are looking for an enthusiastic and conscientious individual to join our Guest Experience team to support with all things Service and Matchday Experience.</p>\n\n\n\n<p>This is an annualised hours position with a larger percentage of hours worked in the Summer months during the Cricket Season and less in the winter months. Working hours would include some evenings and weekends to meet business demands.</p>\n\n\n\n<p>The role has two main focus areas:</p>\n\n\n\n<p><strong>Matchday Experience &nbsp;</strong></p>\n\n\n\n<p><strong>Key Responsibilities</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Support the Head of Guest Experience with the preparations and delivery of the Matchday experience.</li>\n\n\n\n<li>Assist with the booking and coordination of Matchday suppliers, entertainers and volunteers. This will include preparing schedules and briefing documents in advance.</li>\n\n\n\n<li>Attend meetings as required relating to the Matchday Experience.</li>\n\n\n\n<li>Actively talk to guests, identifying individual needs and responding appropriately</li>\n\n\n\n<li>Regularly spot check guest areas and facilities addressing/reporting anything that detracts from the Guest Experience.&nbsp;</li>\n\n\n\n<li>Liaise with the Marketing Team to ensure consistency between the Fan Village experience and in-bowl experience.</li>\n\n\n\n<li>Work with several departments including Food/Beverage, Facilities, Stewarding and Operations to Manage issues and keep our guests at the heart of what we do.</li>\n</ul>\n\n\n\n<p><strong>Club Relations/Guest Service (Non-Matchday)</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li><strong>Key Responsibilities</strong></li>\n\n\n\n<li>Work closely with the Clubs Ticket and Memberships departments and offer assistance to ticket and membership holders (training on the Clubs ticketing system will be provided) The role holder will be required to own Members enquiries from start to finish and offer solutions at the same level as the ticket office.</li>\n\n\n\n<li>Offering a here to help service both internally and externally. This includes (but not limited to) Email, telephone, social media and in person.</li>\n\n\n\n<li>Ensure a positive outcome for any guest queries and complaints in an efficient and timely manner.</li>\n\n\n\n<li>Ensure that opening and closing procedures are followed and actioned daily.</li>\n\n\n\n<li>Assist other club departments as necessary (this could include the cloakroom and Club presentation)</li>\n\n\n\n<li>Proactively handle car park access and egress enquiries.</li>\n\n\n\n<li>Process the Clubs incoming and outgoing post/parcels.</li>\n\n\n\n<li>Work closely with the Clubs Conference and Events departments and offer assistance with the booking of meeting rooms and executive boxes (Training on the clubs booking system will be provided)</li>\n\n\n\n<li>Log and handle the Clubs lost property</li>\n\n\n\n<li>Input data and compile responses to guest enquires and complaints using the Clubs CX system.</li>\n\n\n\n<li>Act as Fire Marshall in the event of an emergency.</li>\n\n\n\n<li>Act as a duty First Aider on a roster basis.</li>\n</ul>\n\n\n\n<p><strong>Benefits</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Holiday allowance increasing with service</li>\n\n\n\n<li>2 x standard Lancashire Cricket season memberships</li>\n\n\n\n<li>Social events throughout the year</li>\n\n\n\n<li>Basic contributory pension scheme with option to join enhanced pension scheme, including life assurance, after 3 months service</li>\n\n\n\n<li>Optional health cash plan scheme</li>\n\n\n\n<li>Discounts at the Lancashire Cricket Official Store, Trafford Cricket Centre, Hilton Garden Inn Emirates Old Trafford, Caff Nero and with the Clubs partners</li>\n\n\n\n<li>Free parking</li>\n</ul>\n\n\n\n<p>Please take a look at the full job description and, if you have the experience and skills required, please submit your CV along with any other required or relevant documentation.</p>\n\n\n\n<p><em>We look forward to hearing from you if you have skills that support our future vision. Lancashire Cricket is an equal opportunities organisation, and we are committed to providing new opportunities and striving for greater diversity.</em></p>\n\n\n\n<p><em>It is a priority for Lancashire Cricket to ensure our Club appropriately reflects the wider communities across the North West and as we strive to meet Sport England Guidance in this regard, we would welcome applications from individuals with the appropriate skills and experience that can also enhance our current diversity mix at the Club.&nbsp;</em></p>\n\n\n\n<p><em>We welcome applications from all ages and backgrounds and are looking for candidates who can contribute diversity of representation and thinking to Lancashire Cricket.&nbsp;Please also be aware, all of our roles are subject to reference checks from previous employers.</em></p>\n\n\n\n<p><strong><em>Lancashire Cricket Club and the Hilton Garden Inn are committed to safeguarding children and adults at risk. Due to this post being in regulated activity, the potential employee will be required to undertake a Disclosure and Barring Service check at the appropriate level. It is a criminal offence for a barred</em>&nbsp;<em>person to attempt to engage with vulnerable groups. The possession of a criminal record will not necessarily prevent an applicant from obtaining this post, as all cases are judged individually according to the nature of the role and information provided.</em></strong></p>\n',
