      '\n<p><strong>Information:</strong></p>\n\n\n\n<p>BTCC is seeking a dedicated and dynamic individual to join our team as the 1st XI Captain and Club Coach for the 2025/26 season.<br>We are looking for a candidate who is eager to grow as a leader and coach. The successful applicant will be provided with ongoing guidance and mentorship in their role, helping them to develop both as a captain and a coach.<br>Our club is home to a young and enthusiastic playing group, with our 1st XI currently competing in the Howard Wilson Shield, the second grade of the Box Hill Reporter District Cricket Association (BHRDCA).</p>\n\n\n\n<p><strong>Key Selection Criteria:</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Proven leadership abilities and experience in a captaincy role</li>\n\n\n\n<li>Strong playing record and technical ability</li>\n\n\n\n<li>Excellent communication skills, with the ability to collaborate effectively with fellow club leaders both on and off the field</li>\n\n\n\n<li>Strategic planning capabilities to continue the clubs on-field success, while nurturing and developing both established players and those transitioning from juniors to senior ranks</li>\n</ul>\n',
