      '\n<p>The European T20 Premier League (ETPL) is an exciting new franchise-based tournament set to launch in July 2025. The league is designed to bring high-quality T20 cricket to Europe.<br><br>To ensure the successful setup and operation of the ETPL, we are seeking an experienced HR and Payroll Coordinator to join our team. With a view to commencing in April 2025, this is a contract role for 9 months</p>\n\n\n\n<p>KEY RESPONSIBILITIES<br>HR Coordination<br> Serve as the first point of contact for HR-related queries, ensuring timely and effective resolution.<br> Provide expert HR advice and guidance to employees, fostering strong workplace relationships.<br> Lead and manage recruitment campaigns, ensuring a seamless hiring process.<br> Support Line Managers with performance management, employee relations, resource planning, and staff development.<br> Ensure compliance with HR policies and employment legislation, updating and enforcing policies as needed.<br> Oversee HR system, Bright HR, ensuring data integrity and compliance.<br> Generate HR reports to support strategic decision-making.<br> Assist in HR projects and provide administrative support as required.</p>\n\n\n\n<p>Payroll Coordination<br> Manage monthly payroll data collection and processing for employees across both Northern and Republic of Ireland.<br> Verify and update employee records, including new hires, leavers, salary adjustments, and working hours.<br> Act as a point of contact with external payroll providers.<br> Address payroll discrepancies and respond to employee payroll queries.<br> Prepare and distribute payroll reports for senior management.</p>\n\n\n\n<p>Remuneration<br> A competitive remuneration package will be on offer to successful candidate.</p>\n\n\n\n<p>Important Notice: Candidates must have valid work authorisation. Due to the shortterm nature of these roles, we are unable to provide work permit or visa sponsorship.<br><br>Cricket Ireland is an equal opportunities employer.</p>\n',
