    description:
      '\n<p>Kuwait Cricket Club (KCC) is excited to announce new opportunities for highly qualified professionals to join its high-performance setup. We are currently hiring for the positions of Assistant Coach and Fielding Coach to support the ongoing development of the Kuwait National Mens and Womens Teams.</p>\n\n\n\n<p>With an eye firmly set on qualifying for the ICC T20 World Cup (Mens) in India 2026, and with the final qualification round scheduled for October 2025 in Muscat, this move is part of KCCs broader commitment to elite performance and international competitiveness.</p>\n\n\n\n<p>In 2025, our national squads of both mens and womens have an ambitious international calendar that includes tours to Hong Kong, Thailand, Qatar, Jersey, Oman, Botswana, and Saudi Arabia. These tours are integral to global pathway events such as the ICC World Cups, Challenge Leagues, and Asia Cups.</p>\n\n\n\n<p>To meet these competitive demands, KCC is looking to enhance its coaching panel by integrating experienced overseas professionals alongside local talent, including former national players of Kuwait. The goal is to bring in international expertise capable of elevating team performance at the highest level.</p>\n\n\n\n<p>Applications are open now for motivated and qualified coaches across Asia and beyond.</p>\n',
