    jobTitle: 'Kitchen Hand (Casual)',
    location: 'Location : Australia',
    address: 'Address : East Perth, Western Australia, 6004, Australia',
    salary: 'Salary : Depending on experience',
    contractLength: 'Contract length : Full Time',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/03/1-16.jpeg',
    email: null,
    website: null,
    contactNumber: null,
    description:
      '\n<p><em><strong>We are hiring now</strong></em><strong><em>!</em></strong></p>\n\n\n\n<p>Do you enjoy working in the kitchen and being part of a team that creates amazing food experiences?&nbsp; As a Kitchen Hand, you will play a crucial role in supporting our kitchen operations at the iconic WACA Ground.<br><br>Some of the tasks that will be asked include:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Maintain cleanliness and organisation of the kitchen and work areas;</li>\n\n\n\n<li>Ensure kitchen equipment is properly cleaned, maintained, and stored after use;</li>\n\n\n\n<li>Help with the receipt and storage of kitchen supplies and ingredients;</li>\n\n\n\n<li>Support the kitchen team with various tasks as needed;</li>\n\n\n\n<li>Follow food safety and hygiene standards;</li>\n\n\n\n<li>Work closely with chefs, cooks, and other kitchen staff to ensure smooth kitchen operations; and</li>\n\n\n\n<li>Assist in setting up and closing down the kitchen before and after service periods.</li>\n</ul>\n\n\n\n<p>To be successful in this role you will have previous experience in assisting in the operation of multi outlet production and dining kitchens, flexibility to work various shifts, including evenings and weekends, ability to work under pressure in a fast paced environment,&nbsp;excellent communication skills, attitude and a team player.&nbsp;</p>\n\n\n\n<p><strong>A little bit about us</strong></p>\n\n\n\n<p>At WA Cricket we aim to be leaders in Australian sport and pride ourselves on developing inspiring players, creating a fun and inclusive community and delivering social outcomes through cricket. The iconic WACA Ground is home to the Perth Scorchers, WA Mens and Womens Teams and the WA Cricket Foundation.</p>\n\n\n\n<p><strong>Our Culture</strong></p>\n\n\n\n<p>At WA Cricket we are committed to nurturing a diverse and inclusive workforce and offer equal opportunities to all.&nbsp;We embrace diversity and encourage applications from people of all ages, ethnicities, abilities, sexual orientations and gender identities.&nbsp; We measure ourselves on our behaviours of Excellence, Collaboration, Inclusion and Celebration.</p>\n\n\n\n<p><strong>Ready to Join Our Team?</strong></p>\n\n\n\n<p>If you are excited about this casual opportunity, we would love to hear from you.&nbsp;<br><br>We are currently only taking applications from candidates who have Australian working rights.</p>\n',
    link: 'https://cricket.jobs/job-listing/kitchen-hand-casual/',
