    jobTitle: 'Head Coach/Development Officer  Renfrew CC',
    location: 'Location : United Kingdom',
    address:
      'Address : Park Road, 17, Stroud, England, GL6 0HR, United Kingdom',
    salary: 'Salary : Depending on experience',
    contractLength: 'Contract length : Full Time',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/03/1-12.jpeg',
    email: null,
    website: null,
    contactNumber: null,
    description:
      '\n<p>Renfrew Cricket Club, currently competing in Division 1 of the Western District Cricket Union (WDCU), is seeking to appoint a Head Coach/Development Officer with a minimum qualification of ECB Level 2 or an equivalent coaching accreditation.<br><br>This role will be pivotal in supporting the development of the clubs current adult and junior members, attracting new players, and working collaboratively with the senior and junior cricket convenors to strengthen the clubs overall performance</p>\n\n\n\n<p><strong>Key Responsibilities:</strong><br><br><strong>Head Coach:</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Collaborate with the Senior Convenor to establish clear development pathways for players progressing through the senior teams, identifying and nurturing talent.</li>\n\n\n\n<li>Provide input into senior team selection and work closely with senior captains to implement the approved development pathway.</li>\n\n\n\n<li>Support match-day duties for the first XI in partnership with the team captain.</li>\n\n\n\n<li>Develop and oversee a comprehensive winter training and development programme for senior and junior players, including the establishment of an academy offering group and one-to-one coaching sessions to generate additional income.</li>\n\n\n\n<li>Deliver the fourth year of the clubs successful Introduction to Cricket for Adults programme and support the development of veterans cricket.</li>\n\n\n\n<li>Lead the creation and implementation of a womens cricket programme</li>\n</ul>\n\n\n\n<p><strong>Development Officer:</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Work alongside the Junior Convenor and Active Schools coordinators to increase participation across boys and girls age groups, from All Stars/Dynamos to junior teams.</li>\n\n\n\n<li>Organise and deliver development initiatives, including school festivals and summer camps</li>\n</ul>\n\n\n\n<p><strong>Performance Monitoring:</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Key performance indicators (KPIs) will be established to track progress and achievement of the outlined objectives.</li>\n</ul>\n\n\n\n<p><strong>Working Hours and Remuneration:</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>The role requires flexibility, with an average of 37.5 hours per week to accommodate the clubs requirements.</li>\n\n\n\n<li>Remuneration will be negotiable based on the experience and credentials of the successful applicant.</li>\n</ul>\n',
    link: 'https://cricket.jobs/job-listing/head-coach-development-officer-renfrew-cc/',
