    jobTitle: 'Administration, Cricket & Venue Operations',
    location: 'Location : New Zealand',
    address: 'Address : Seddon Road, 50, Hamilton, Waikato, 3204, New Zealand',
    salary: 'Salary : Depending on experience',
    contractLength: 'Contract length : Full Time',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/03/1-7.png',
    email: 'careers@ndca.co.nz',
    website: null,
    contactNumber: null,
    description:
      '\n<p><strong>PURPOSE</strong><br>-To undertake a comprehensive range of administrative duties for the organisation, to a high standard and as required, ensuring the needs of the organisation are met.<br>-To provide high quality executive secretarial support to the CEO and Executive team as required. The purpose is also to ensure the efficient management and operation of the office and requirements.<br>-To lead all cricket and venue operations including pathway/talent, domestic and international, in preparation to deliver all cricket held at Seddon Park.<br>-To lead and coordinate required cricket operations, for all Northern Districts or Northern Brave matches outside of Seddon Park.</p>\n\n\n\n<p><strong>SPECIFIC DUTIES &amp; RESPONSIBILITIES</strong><br><br><strong>Administration</strong><br>-Maintain efficient office procedures for filing and record management.<br>-Undertake other duties such as banking, credit control or payroll functions.<br>-Processing invoices/purchase orders.<br>-Assisting with all aspects of administrative management, directory maintenance, logistics, equipment inventory and storage.<br>-Managing inventory of assets and supplies, sourcing for suppliers (vendors) and submitting invoices<br>-Assisting with funding applications and accountabilities.<br>-Coordinating between departments and operating units in resolving day-to-day administrative and operational problems.<br>-Scheduling and coordinating meetings, interviews, events and other similar activities.<br>-Preparing business correspondence, typically using Microsoft Office (Word, Excel, PowerPoint, Access, Outlook).<br>-Sort and distribute incoming mail to areas and staff within the organisation and dispatch outgoing mail.<br>-Lead all constitutional and administrative requirements including minute taking for AGM, Board meetings, staff meetings, forums, appointment panels etc.<br>-Ensure all constitutional meetings and administrative requirements of such are delivered in a timely manner<br>-Support staff in assigned project-based work.<br>-Organisation of special events.<br>-Booking travel and accommodation and other ad-hoc duties as required.<br><br><strong>Cricket and Venue Operations</strong><br>-Lead the logistics and delivery of match day requirements for all NDCA home fixtures and Seddon Park international fixtures in conjunction with NZC and H3.<br>-Liaise with stakeholders to coordinate key deliverables such as catering, match managers, match officials, catering, match day staff including casual event staff, net bowlers, changing room attendants, and other Major Associations and NZC.<br>-Responsible for the opening and closing of the venue for match days and training days.<br>-Ensure all match day requirements are met e.g., infrastructure is in place, medical plan, post-match clean, etc.<br>-Manage all Seddon Park cricket bookings in conjunction with H3, including Pay2Play and Seddon Park indoor centre bookings.<br>-Manage budget for match day delivery (Match Running Budget).<br><br><strong>Executive Team Support</strong><br>-Preparation of agendas and attending meetings to take action points as required.<br>-Carrying out background research and presenting findings.<br>-Producing documents, briefing papers, reports and presentations.<br>-Liaising with clients, suppliers and other staff on behalf of the executive team.</p>\n\n\n\n<p><strong>GENERAL DUTIES &amp; RESPONSIBILITIES</strong><br><br>-Be punctual and work the hours and times specified.<br>-Prioritise workload to ensure work of the greatest importance to the business is undertaken with urgency and to a high standard.<br>-Support and help develop a positive workplace culture.<br>-Demonstrate excellent interpersonal communication skills.<br>-Responsibly manage all business resources within accountability levels.<br>-Undertake all duties and responsibilities outlined in this job description and all other duties as required by the business.<br>-Comply with all employment obligations.<br>-Promptly undertake to complete all reasonable and lawful instructions and directions given.<br>-Serve the business in good faith, promoting and protecting the businesss best interests.<br>-During work time, and such other times as may be reasonably required, dedicate all effort to the execution and fulfillment of the duties, responsibilities, obligations, and instructions related to employment.<br>-Demonstrate through own actions a commitment to Health and Safety at work when undertaking work or observing others in the workplace.</p>\n\n\n\n<p>We will be reviewing applications and conducting interviews on a rolling basis. Please note that the position may be filled at any time, so we encourage you to apply early. Due to the high number of applications we typically receive, please note that only shortlisted applicants will be contacted.</p>\n\n\n\n<p>Please note that the successful candidate must have the legal right to work in New Zealand, hold a full driver licence and consent to a police vet.</p>\n\n\n\n<h3 class="wp-block-heading">Related Documents</h3>\n\n\n\n<ul class="wp-block-list">\n<li><a href="https://www.ndcricket.co.nz/file/admin-cricket-venue-ops-jd/open" target="_blank" rel="noreferrer noopener">Admin Cricket Venue Ops Jd&nbsp;(24 kb)</a></li>\n</ul>\n',
    link: 'https://cricket.jobs/job-listing/administration-cricket-venue-operations/',
