    jobTitle: 'Casual Community Coach',
    location: 'Location : United Kingdom',
    address:
      "Address : Saint John's Wood Road, London, England, NW8 8QN, United Kingdom",
    salary: 'Pay rate : 25 per hour',
    contractLength: 'Contract length : Contract',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/03/1-8.jpeg',
    email: 'recruitment@middlesexccc.com',
    website: null,
    contactNumber: null,
    description:
      '\n<p><strong>Who are we:</strong></p>\n\n\n\n<p>Middlesex in the Community are the charitable arm of Middlesex Cricket. We promote and deliver cricket across the 17 London boroughs that form the cricket County of Middlesex.</p>\n\n\n\n<p>We are looking for Casual Community Coaches to join our team*. You will be part of the Middlesex in the Community team who work in one of the most vibrant cities in the world, London! We deliver participation focused cricket, working with local schools, youth groups, community engagement programmes and charities to allow everyone to take part in the game. We are a charity that embraces difference, pushes the boundaries on what can be achieved and wants to lead on the world stage. We believe in the power of sport, in its ability to unify communities, develop people and improve environments so that everyone can take part and thrive in cricket!</p>\n\n\n\n<p>*Please note that these roles are to work as self-employed contractors, employment contracts are not available.<br></p>\n\n\n\n<p><strong>Principal responsibilities</strong><strong></strong></p>\n\n\n\n<p>The purpose of this role is to deliver Community Coaching across a range of Middlesex in the Community sessions which could include but is not limited to mainstream/SEND schools, street cricket sessions, Super 1s inclusive cricket and walking cricket. All coaches are expected to represent Middlesex cricket professionally. This means that the cricket sessions would be delivered in a safe way adhering to all ECB regulations. Our aim is to make cricket fun and inclusive, we want your help on delivering cricket in this way. There are training and CPD opportunities that you would be expected to attend were relevant and suggested be MITC staff.</p>\n\n\n\n<p>Please note that you will be required to complete and return a Safer Recruitment Application Form if you are shortlisted and invited to interview.</p>\n\n\n\n<p><br>The interview process may include both a panel interview to discuss your experience and a practical interview at a current MITC session.</p>\n\n\n\n<p>Please note that due to the volume of applications the Club receives, we are unable to provide specific feedback on unsuccessful applications. All applicants must have Right to Work in the UK, sponsorship for overseas employees cannot be provided for this role.</p>\n\n\n\n<p>Middlesex in the Community (MITC) is committed to safeguarding and protecting the children, young people and adults at risk that we work with. As such, all posts are subject to a safer recruitment process, including the disclosure of criminal records, vetting checks and the seeking of references. We ensure we have a range of policies and procedures in place which promote safeguarding and safer working practice across our services.</p>\n\n\n\n<p>MITC are equal opportunity employers and prohibit discrimination and harassment of any kind. We are committed to the principle of equal employment opportunity for all employees and to providing employees with a work environment free of discrimination and harassment. MITC will ensure that all existing and potential employees receive equal consideration, and is committed to the elimination of unlawful or unfair discrimination on the grounds of the Equality Act 2010.</p>\n\n\n\n<p>As users of the disability confident scheme, we guarantee to interview all disabled applicants who meet the minimum criteria for the vacancy.</p>\n\n\n\n<p>MITC aim for its work force, at all levels, to reflect the composition of the Citys population. To achieve this, MITC will take active and positive steps to eliminate discrimination, reduce the effects of past discrimination and to promote equality in employment.</p>\n\n\n\n<p>The Clubs Privacy notice can be viewed&nbsp;<strong><a href="https://www.middlesexccc.com/page/privacy-policy">here</a>.</strong></p>\n',
