    contactNumber: null,
    description:
      '\n<h2 class="wp-block-heading">The Opportunity</h2>\n\n\n\n<p>The Yorkshire Cricket Board, in partnership with the Lords Taverners wish to appoint a SEND Coach to help deliver Lord Taverners disability programmes (Super 1s and Disability Schools Cricket Programme) across Yorkshire. The aim of the role is to deliver the aims and outcomes of the SEND programme incorporating both table cricket and soft-ball cricket where appropriate whilst continuing to work with and engaging with new SEND settings across Yorkshire. The role will also focus on ensuring more young people with SEND have the opportunity to engage in the most appropriate form of cricket and to transition more young people with a disability to community settings.</p>\n\n\n\n<h2 class="wp-block-heading">Details and Requirements</h2>\n\n\n\n<p>Hourly rate: 20</p>\n\n\n\n<p>Contract: Zero-hours</p>\n\n\n\n<p>DBS: Enhanced Required</p>\n\n\n\n<p>First Aid Certificate: Required</p>\n\n\n\n<p>Coaching Qualification: Required</p>\n\n\n\n<p>Safeguarding Qualification: Required</p>\n\n\n\n<p>The Yorkshire Cricket Board follows fairer and safer recruitment practices.&nbsp;Yorkshire Cricket is committed to safeguarding children and adults. Due to this role being in regulated activity, the postholder will be required to undertake an ECB Enhanced Disclosure and Barring Service (DBS) Check; it is a criminal offence for a barred<strong>&nbsp;</strong>person to attempt to engage with vulnerable groups. The possession of a criminal record will not necessarily prevent an applicant from obtaining this post, as all cases are judged individually according to the nature of the role and information provided.</p>\n\n\n\n<p>Only applicants who submit an application by the closing date will be considered. Shortlisted candidates will be invited to participate in further selection phases including a formal interview. The successful candidate must have a satisfactory ECB enhanced DBS check as a condition of employment.</p>\n\n\n\n<p>Should you have any questions, require any reasonable adjustments to support your application, or wish to arrange a confidential discussion, please do not hesitate to contact&nbsp;<a href="mailto:vacancies@yorkshireccc.com">vacancies@yorkshireccc.com</a>.</p>\n\n\n\n<p></p>\n',
