    jobTitle: 'Digital Content Creator',
    location: 'Location : Australia',
    address: 'Address : Toowong, Queensland, Australia',
    salary: 'Salary : Depending on experience',
    contractLength: 'Contract length : Permanent, Full Time',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/03/1-1.jpeg',
    email: null,
    website: null,
    contactNumber: null,
    description:
      '\n<p>Brisbane Boys College is an established GPS day and boarding school. The school was founded in 1902 by Mr. Arthur Rudd, who had a vision of moulding boys to be worthy leaders and good citizens  a vision that remains today. The College occupies 13-hectare site four kilometres from the centre of Brisbane and educates boys from Prep to Year 12.</p>\n\n\n\n<p><strong>PRIMARY OBJECTIVE</strong></p>\n\n\n\n<p>The Digital Content Creator is responsible for the BBC brands digital presence including paid and organic social media, website management, creating engaging digital content, and email marketing. This role will play a pivotal part in boosting College brand visibility, increasing audience engagement, and aligning content strategies with broader BBC marketing goals. It requires successfully managing expectations and competing deadlines while maintaining strong stakeholder relationships.</p>\n\n\n\n<p><strong>KEY RESPONSIBILITIES</strong><br>Content Creation &amp; Management:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Develop and deliver high-quality, on-brand content for social media channels (images, videos, blogs, posts, newsletters, etc.) to enhance brand visibility and engagement</li>\n\n\n\n<li>Create engaging video content using tools such as iPhone, DJI Osmo Pocket, Adobe CC, Canva, and CapCut, incorporating motion graphics where needed</li>\n</ul>\n\n\n\n<p>Community Engagement:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Foster a vibrant online community by responding to comments, messages, and inquiries while building strong relationships with followers</li>\n\n\n\n<li>Professionally engage with the community on social media pages, groups, WhatsApp groups, and via the group communications email</li>\n</ul>\n\n\n\n<p>Data-Driven Insights:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Monitor and analyse social media performance metrics, digital marketing campaign effectiveness, and ROI using analytics tools</li>\n</ul>\n\n\n\n<ul class="wp-block-list">\n<li>Provide insights and recommendations based on data to refine content strategies, optimise engagement, and improve paid and organic content performance</li>\n</ul>\n\n\n\n<p>Trend Awareness &amp; Innovation:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Stay ahead of social media trends, emerging platforms, and digital marketing best practices, offering innovative strategies to keep the brand competitive</li>\n\n\n\n<li>Act as a go-to resource for identifying new trends, technologies, and opportunities to enhance digital presence</li>\n</ul>\n\n\n\n<p>Brand Consistency:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Ensure all content adheres to brand guidelines, tone, and visual identity, maintaining a consistent brand presence across all platforms</li>\n\n\n\n<li>Develop, publish and maintain high quality website content and ensure brand consistency across all digital channels, optimising for SEO and user experience</li>\n\n\n\n<li>Develop digital marketing campaigns to achieve KPIs, measuring effectiveness and ROI</li>\n</ul>\n\n\n\n<p>Campaign Development &amp; Execution:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Plan, develop, and manage digital marketing campaigns across social media, email, and website platforms to achieve KPIs</li>\n\n\n\n<li>Manage Meta Ad campaigns, LinkedIn Campaign Manager, and Google AdWords where required</li>\n</ul>\n\n\n\n<p>Video Production &amp; Editing:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Film, edit, and produce professional-quality video content for social media and digital platforms using tools such as Adobe Premiere Pro, After Effects, Canva, and CapCut</li>\n\n\n\n<li>Implement motion graphics using templates and ensure high production standards</li>\n</ul>\n\n\n\n<p>Project &amp; Stakeholder Management:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Utilise project management tools (e.g., Asana) to manage workflows efficiently</li>\n\n\n\n<li>Collaborate with internal stakeholders to align content and campaigns with BBC goals</li>\n</ul>\n\n\n\n<p>Email Marketing (eDMs):</p>\n\n\n\n<ul class="wp-block-list">\n<li>Develop and manage high-quality email campaigns using eDM platforms, ensuring engaging content and effective audience targeting</li>\n</ul>\n\n\n\n<p>Search Engine &amp; Digital Marketing Optimisation:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Apply SEO, SEM, and keyword research strategies to enhance digital content visibility</li>\n\n\n\n<li>Utilise content management tools and analytics to improve search rankings and digital marketing effectiveness</li>\n</ul>\n\n\n\n<p>MANDATORY REQUIREMENTS</p>\n\n\n\n<ul class="wp-block-list">\n<li>Adhere to the PMSA Code of Conduct</li>\n\n\n\n<li>Comply with College policies, procedures, and training requirements, including those related to occupational health and safety, risk management, child protection, and confidentiality</li>\n\n\n\n<li>Positively support the Colleges traditions and Christian ethos</li>\n\n\n\n<li>It is a condition of employment that the incumbent must apply for and obtain a Blue Card for Working with Children or current registration with the Queensland College of Teachers prior to and while you are employed in the position</li>\n</ul>\n',
