    jobTitle:
      'Independent Member,Governance, Nominations and Elections Committee(Voluntary role)',
    location: 'Location : United Kingdom',
    address: 'Address : Eaton Road, Hove, England, BN3 3AN, United Kingdom',
    salary: 'Pay rate : Positions are voluntary',
    contractLength: 'Contract length : Full Time',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/02/1-24.png',
    email: 'recruitment@sussexcricket.co.uk',
    website: null,
    contactNumber: null,
    description:
      '\n<p>Sussex Cricket is seeking to identify and appoint an independent member to join its Governance, Nominations and Elections Committee.<br><br>The Governance, Nominations and Elections Committee (GNEC) is responsible for developing and maintaining formal, rigorous and transparent procedures to ensure the governance of Sussex Cricket (and its subsidiaries) is of the highest order, that it represents good practice and demonstrates integrity, equality, diversity and inclusiveness. The GNEC is a formal subcommittee of the Sussex Cricket Limited Board and makes recommendations to the SCL Board of Directors on governance issues and on appointments to both the SCL Board and the Board of Trustees of the Sussex Cricket Foundation (SCF).</p>\n\n\n\n<p>Duties of the GNEC include:</p>\n\n\n\n<ul class="wp-block-list">\n<li>regularly review the role, structure, size and composition (including the skills, knowledge, experience and diversity) of the SCL Board, its Committees and the SCF Board of Trustees and, in light of these evaluations, make recommendations to both organisations on any changes that are considered necessary with regard to their respective roles, procedures and capabilities;</li>\n</ul>\n\n\n\n<ul class="wp-block-list">\n<li>give full consideration to succession planning and make recommendations to both organisations concerning the formulating of plans for succession of Non-Executive Directors of the SCL Board and the SCF Trustees;</li>\n</ul>\n\n\n\n<ul class="wp-block-list">\n<li>with regard to the election of SCL Directors, review all candidates nominated by Individual Members and Affiliated Clubs, to ensure that the candidates comply with the eligibility criteria as prescribed within the SCL Rules and Regulations</li>\n</ul>\n\n\n\n<ul class="wp-block-list">\n<li>be responsible for identifying and recommending possible candidates for the position of SCF Trustee to the SCL Board, which shall be responsible for appointing such Trustees;</li>\n</ul>\n\n\n\n<ul class="wp-block-list">\n<li>be responsible for identifying, recommending and reviewing all candidates nominated for the President and Vice President positions and to ensure that the candidates comply with the eligibility criteria as prescribed within the SCL Rules and Regulations</li>\n</ul>\n\n\n\n<ul class="wp-block-list">\n<li>be responsible for overseeing the integrity of the election process for SCL Directors, including the identification of a schedule of competencies, the preparation of<br>application forms, the issuing of guidance regarding canvassing and electioneering, the<br>undertaking of fit and proper persons reviews, and any such other activities to ensure<br>that elections are undertaken fairly;</li>\n</ul>\n',
