    description:
      '\n<p><strong>About Football Radar</strong></p>\n\n\n\n<p>For over a decade, we have combined predictive modelling techniques with expert analysis and our proprietary datasets to deliver insights that drive success for our betting clients and clubs. By combining the agility of a start-up with the stability of an established business, weve created an environment where innovation and long-term success go hand in hand.</p>\n\n\n\n<p><strong>About The Role</strong></p>\n\n\n\n<p>As the lead of our cricket team, you will help develop models to turn cricket insights into probabilistic predictions for use in the betting markets. We are looking for smart, ambitious people who enjoy solving challenging problems, and are able to make pragmatic decisions in a dynamic environment. You will have the opportunity to help set the direction of our research, and the freedom to explore and develop your own ideas.&nbsp;</p>\n\n\n\n<p>You will be based at our London office, at 1 Craven Hill, London, W2 3EN, with the option to work from home one day a week. While we are open to flexible working hours to help you avoid rush hour, we value in-person collaboration and learning opportunities, so we are not considering fully remote candidates at this time.</p>\n\n\n\n<p><strong>What We Offer</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Half yearly bonus opportunities based on company performance</li>\n\n\n\n<li>33 days holiday (Including bank holidays)</li>\n\n\n\n<li>Competitive contribution matched pensions</li>\n\n\n\n<li>Health and well-being benefits:\n<ul class="wp-block-list">\n<li>Private Medical Insurance (including excess coverage)</li>\n\n\n\n<li>Health Cash Plan via Bupa</li>\n\n\n\n<li>Subsidised gym membership</li>\n</ul>\n</li>\n\n\n\n<li>Daily subsidised office meals</li>\n\n\n\n<li>Learning and development budgets to invest in your personal growth</li>\n\n\n\n<li>Company and team led engagement activities throughout the year</li>\n\n\n\n<li>Fortnightly five-a-side football game amongst colleagues</li>\n</ul>\n',
    link: 'https://cricket.jobs/job-listing/cricket-quant-lead/',
