      '\n<p>Seaford Tigers Cricket Club (STCC) is excited to announce an opportunity for an experienced and passionate Senior Coach to lead our cricket program for the 2025/26 season and beyond. We are looking for a highly knowledgeable individual with a proven track record in cricket, who can bring their expertise to enhance and develop our senior teams while nurturing the clubs junior program.<br>Applications close COB Friday 28th February. Renumeration to be negotiated. All expressions of interest/applications will be kept confidential.</p>\n\n\n\n<p><strong>Key Selection Criteria:</strong></p>\n\n\n\n<p>*Opportunity to shape and elevate the clubs senior cricket program<br>*Working closely with our dedicated assistant coaches, junior development coordinators, players, and committee.<br>*Integral to fostering a positive, competitive environment that helps both senior and junior players develop and reach their full potential.<br>*Ready to take on this exciting challenge and help shape the future of Seaford Tigers Cricket Club</p>\n',
