    jobTitle: 'Competition Administrator',
    location: 'Location : Australia',
    address: 'Address : Albion, Queensland, Australia',
    salary: 'Salary : Depending on experience',
    contractLength: 'Contract length : Full Time',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/02/1-20.png',
    email: null,
    website: null,
    contactNumber: null,
    description:
      '\n<p>Join our team as a Competition Administrator based on a 12-month fixed-term basis commencing in March, based at Queensland Crickets head office in Albion.<br><em><br>As competitions run over weekends, candidates must be available to work weekends, some public holidays, and some nights especially during cricket season (October  March).&nbsp;&nbsp;</em></p>\n\n\n\n<p><strong>About the role&nbsp;</strong><br>Reporting to the Systems &amp; Competitions Manager, the Competition Administrator will play a crucial role in supporting Queensland Cricket (QC) to achieve key strategic objectives of growing participation across the state, whilst ensuring high-quality administration of competitions.&nbsp;<br><br>Your key responsibilities will include:&nbsp;</p>\n\n\n\n<ul class="wp-block-list">\n<li>Competition Management &nbsp;Youll contribute to reaching participation goals by managing stable, sustainable and professional competitions. Youll plan competitions in accordance with the Australian Cricket Pathway, coordinate match day communications, deliver high-quality club training and support, ensuring the smooth operations of competitions.</li>\n\n\n\n<li>&nbsp;Sponsor Servicing &amp; Integration &nbsp;Youll promote and develop strong relationships with partners and sponsors of competitions across QLD to achieve positive, two-way value for both QC and its partners.</li>\n\n\n\n<li>&nbsp;Systems and Processes&nbsp; Youll act as a key contact in providing systems (PlayHQ) support for clubs and volunteers, oversee online registration processes, and collaborate across QC to ensure stakeholders have access to the training required. Youll play a pivotal role in ensuring all stakeholders have access to the right information through well-developed processes and resources.&nbsp;</li>\n</ul>\n\n\n\n<p>About Queensland Cricket&nbsp;<br>Queensland Cricket is responsible for leading and administering cricket throughout Queensland. Our purpose is to promote and grow cricket ensuring all Queenslanders can play, participate, and enjoy the game. Our flagship teams include Queensland Bulls, Queensland Fire and Brisbane Heat.&nbsp;<br><br>Queensland Cricket values diversity and is an equal opportunity employer. Your age, gender, sexual orientation, disability or religion wont impact whether youre part of the team; its your drive to do great work thatll help you shine.&nbsp;</p>\n\n\n\n<p><strong>Keen to join the team?&nbsp;</strong><br>Click apply to submit your application, which includes a cover letter outlining:&nbsp;</p>\n\n\n\n<ul class="wp-block-list">\n<li>&nbsp;What excites you about joining the team at QC&nbsp;</li>\n\n\n\n<li>&nbsp;The skills, experience and qualities that make you the best candidate.&nbsp;</li>\n</ul>\n\n\n\n<p>&nbsp;Well review applications as theyre received, so please apply early!&nbsp;<br><br>Our values &nbsp;<em>We Care, We Lead, We Deliver</em>, and&nbsp;<em>We Innovate</em>&nbsp; are embedded in everything we do. Were eager to meet people who can contribute to our team in a variety of ways, not just candidates who check all the boxes. Even if you dont feel you meet every requirement for this role, we still encourage you to apply.&nbsp;<br><br>We require all applicants to undergo background checks and screening prior to or during any appointments. Queensland Cricket is committed to providing a safe environment for everyone involved in our sport, including children.&nbsp;<br><br>Queensland Cricket acknowledges that we reside on traditional lands of the Yuggera people and the Turrbal people. We offer our respect to the Elders of these traditional lands, and through them to all Aboriginal and Torres Strait Islander peoples past, present, and emerging.&nbsp;&nbsp;</p>\n',
    link: 'https://cricket.jobs/job-listing/competition-administrator/',
