    description:
      '\n<p><strong>Job Opportunity: Cricket Coaches Wanted at Esher Cricket Club</strong></p>\n\n\n\n<p>Esher Cricket Club, a premier league club based in the South West London/North Surrey area, is currently seeking <strong>UK-based qualified cricket coaches</strong> to support our thriving youth section during the upcoming season.</p>\n\n\n\n<h3 class="wp-block-heading"><strong>About Us</strong></h3>\n\n\n\n<p>We are one of the largest cricket clubs in the region, with a strong reputation for developing young talent. Our youth section boasts over <strong>700 members</strong>, making it one of the most active junior cricket programs in the country.</p>\n\n\n\n<h3 class="wp-block-heading"><strong>Coaching Details</strong></h3>\n\n\n\n<ul class="wp-block-list">\n<li><strong>Duration:</strong> 15-week programme</li>\n\n\n\n<li><strong>Start Date:</strong> End of April</li>\n\n\n\n<li><strong>Coaching Schedule:</strong>\n<ul class="wp-block-list">\n<li><strong>Mondays, Wednesdays, and Fridays:</strong> 6:15 PM  8:15 PM</li>\n\n\n\n<li><strong>Tuesdays:</strong> 5:30 PM  8:30 PM</li>\n\n\n\n<li><strong>Sundays:</strong> 8:30 AM  12:00 PM</li>\n</ul>\n</li>\n</ul>\n\n\n\n<h3 class="wp-block-heading"><strong>Requirements</strong></h3>\n\n\n\n<ul class="wp-block-list">\n<li>Must hold a <strong>UK coaching qualification</strong> or equivalent in cricket</li>\n\n\n\n<li>Passion for developing young cricketers</li>\n\n\n\n<li>Ability to engage and inspire young players</li>\n\n\n\n<li>Strong communication and teamwork skills</li>\n</ul>\n\n\n\n<h3 class="wp-block-heading"><strong>What We Offer</strong></h3>\n\n\n\n<ul class="wp-block-list">\n<li>Competitive <strong>hourly pay</strong></li>\n\n\n\n<li>The opportunity to coach at one of the <strong>leading cricket clubs</strong> in the region</li>\n\n\n\n<li>A vibrant, professional, and rewarding coaching environment</li>\n</ul>\n\n\n\n<p>If you are interested in joining our coaching team, please reach out to <strong>Duncan Pauline</strong> for further details.</p>\n\n\n\n<p>We look forward to hearing from enthusiastic coaches who are ready to make a difference in youth cricket!</p>\n',
