      '\n<p><strong> Cricket Coaching Opportunity  South West London</strong></p>\n\n\n\n<p>Cricket tots is looking for an enthusiastic and qualified coach to join our team for <strong>Saturday morning sessions</strong> in <strong>South West London</strong>. This is a fantastic opportunity to inspire and develop young cricketers in a fun and engaging environment.</p>\n\n\n\n<h3 class="wp-block-heading"><strong>Role Details:</strong></h3>\n\n\n\n<p> <strong>Schedule:</strong> Weekends (Saturday mornings)<br> <strong>Qualifications:</strong> ECB Level 1 or above<br> <strong>Experience:</strong> Previous experience working with children is essential<br> <strong>Requirements:</strong> Up-to-date <strong>DBS check</strong> required<br> <strong>Additional Opportunities:</strong> Potential for <strong>1:1 coaching sessions</strong> after the group sessions, plus further work opportunities available</p>\n\n\n\n<p>If youre passionate about coaching and want to help young players develop their skills, wed love to hear from you!</p>\n\n\n\n<p> <strong>Apply now or get in touch for more details!</strong></p>\n\n\n\n<p></p>\n',
