    jobTitle: 'Remote Cricket Coach',
    location: 'Location : United Kingdom',
    address: 'Address : United Kingdom',
    salary: 'Salary : Depending on experience',
    contractLength: 'Contract length : Full Time',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/01/1-29.jpeg',
    email: 'info@allroundcricketcoaching.com',
    website: null,
    contactNumber: '07840273845',
    description:
      '\n<p><strong>Job Description:</strong><br>We are seeking passionate and skilled Cricket Coaches to join our remote coaching team. The<br>ideal candidates will have a strong background in cricket, a commitment to teaching, and the<br>ability to engage with players of all ages and skill levels. This role offers flexible, casual hours,<br>making it perfect for those looking to share their expertise while managing other commitments.<br><strong>Key Responsibilities:</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>Deliver high-quality cricket coaching sessions remotely, tailored to the needs of individual<br>players or groups.</li>\n\n\n\n<li>Develop and implement training programs that focus on skill development, fitness, and game<br>strategies.</li>\n\n\n\n<li>Provide constructive feedback to players, helping them improve their skills and confidence.</li>\n\n\n\n<li>Engage with players and parents to discuss progress and areas for improvement.</li>\n\n\n\n<li>Stay updated on the latest coaching techniques, cricket regulations, and trends in the sport.</li>\n\n\n\n<li>Promote a positive and inclusive environment that encourages a love for cricket.<br><br>Relevant coaching certifications (e.g., ECB Level 2 or higher)<br> must possess a current enhanced DBS certificate.<br> Safeguarding certificate</li>\n</ul>\n\n\n\n<p>A passion for cricket and a desire to foster talent in others</p>\n\n\n\n<p><strong>What We Offer:</strong><br>Flexible working hours that fit your schedule<br>The opportunity to work with a diverse range of players and help them achieve their cricketing goals.<br>Ongoing professional development and training opportunities.<br>A supportive and dynamic team environment.<br> uniform provide</p>\n\n\n\n<p>Join Allround Cricket Coaching and be part of a community dedicated to making cricket<br>accessible and enjoyable for everyone!</p>\n',
    link: 'https://cricket.jobs/job-listing/remote-cricket-coach/',
