    description:
      '\n<p>Are you a cricket enthusiast with a passion for coaching and inspiring the next generation of<br>players? Cricket Scotland is seeking dynamic, skilled, and motivated individuals to join our<br>MCC Foundation Hubs Programme as Lead Coaches at hubs based in Glasgow, Edinburgh<br>and Perthshire<br><br><strong>About the MCC Foundation Hubs<br></strong>The MCC Foundation Hubs are the flagship programme of the Marylebone Cricket Clubs<br>charitable arm. These hubs provide weekly winter cricket coaching for state school pupils,<br>focusing on increasing opportunities for young people from underserved communities. The<br>programme bridges the gap between participation initiatives and the national pathway,<br>offering a vital platform for nurturing talent and developing future stars.<br><br><strong>As a Lead Coach, you will:</strong></p>\n\n\n\n<ul class="wp-block-list">\n<li>lead on the delivery of high-quality weekly coaching sessions.</li>\n\n\n\n<li>keep an accurate record of attendance and any other admin required.</li>\n\n\n\n<li>in collaboration with the Hub Manager, ensure effective communication to participants parents/carers.</li>\n</ul>\n\n\n\n<p><strong>Key Details</strong><br>These are fixed term opportunities, during 2025. Much of the work will be undertaken<br>between January to March, with additional involvement on competition days thereafter.<br><br>11 coaching sessions will typically be delivered within each hub between January and March,<br>with flexible working hours depending on the hubs.<br><br>Sessions tend to be between 1.5 and 4.5 hours. Full details of times and locations can be<br>found <a href="https://www.cricketscotland.com/Play/Juniors/MCCFoundationHubs" title="">HERE</a>.</p>\n\n\n\n<p>We have vacancies for Lead Coaches for the following squads:</p>\n\n\n\n<ul class="wp-block-list">\n<li>U12 Girls Edinburgh</li>\n\n\n\n<li>U12 and U16 Girls Glasgow</li>\n\n\n\n<li>U14 and U16 Boys Glasgow</li>\n\n\n\n<li>U16 Girls Perthshire</li>\n\n\n\n<li>U14 and U16 Boys Perthshire</li>\n</ul>\n\n\n\n<p>How to Apply<br>To apply for this exciting opportunity, please click Apply Now. You will thereafter provide your<br>contact details and then upload a covering letter onto our recruitment portal outlining:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Cricket Coaching Qualifications and experience.</li>\n\n\n\n<li>The hub location(s) where you would like to work, in Glasgow, Edinburgh and Perthshire.</li>\n\n\n\n<li>Whether you want to work on the Girls or Boys programme and any preference on the age group you would like to work with.</li>\n\n\n\n<li>Your interest in the MCC Foundation Hubs Programme.</li>\n</ul>\n\n\n\n<p>We would also encourage all applications to complete our diversity monitoring form which<br>can be found <a href="https://www.surveymonkey.com/r/ZCVRWP5" title="">HERE.</a></p>\n',
