    description:
      '\n<p><strong>We Are Hiring! Join Our Team at Malaysia Cricket Association </strong></p>\n\n\n\n<p>I am excited to announce that the Malaysia Cricket Association is looking for a dynamic and passionate individual to join us as an Assistant Development Coordinator.</p>\n\n\n\n<p>As part of our commitment to developing and promoting cricket in Malaysia, this role offers a fantastic opportunity to work closely with our development team and contribute to the growth of the sport across the nation.</p>\n\n\n\n<p><strong>Key Responsibilities:</strong><br>Assist in the planning and execution of development programs and initiatives.<br>Coordinate with schools, clubs, and other cricketing bodies.<br>Support the administration and logistics of cricket events and activities.<br>Engage with the cricketing community to promote participation and development.<br>Monitor and evaluate the progress of development programs.<br>Assist in the preparation of reports and documentation for development activities.<br>Support the organization of training sessions, workshops, and seminars.<br>Coordinate volunteer and intern activities related to cricket development.<br>Assist in managing social media and communications for development initiatives.<br><br>If you or someone you know is interested in making a meaningful impact in the world of cricket, please apply by sending your resume to <a href="mailto:rozlan@malaysiacricket.org.my">rozlan@malaysiacricket.org.my</a>.<br>Join us in our mission to elevate Malaysian cricket to new heights!</p>\n',
