    jobTitle: 'Petworth Park Cricket Club  Head Coach',
    location: 'Location : United Kingdom',
    address:
      'Address : Tillington Road, 398, Petworth, England, GU28 0QY, United Kingdom',
    salary: 'Salary : Depending on experience',
    contractLength: 'Contract length : Full Time',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/01/1-22.png',
    email: 'cricket@petworthparkcricket.org',
    website: null,
    contactNumber: null,
    description:
      '\n<p><strong>About Us:</strong><br>Petworth Park Cricket Club (PPCC) is a vibrant community cricket club with a thriving junior<br>section and a competitive adult section competing in the Sussex Cricket League. PPCC is open<br>to all and prides itself on providing a safe, friendly and welcoming environment. The Spirit of<br>Cricket is enshrined in the Club Constitution and players in all sections are expected to buy into<br>these values and play the game in the right way.<br><br>We are seeking an enthusiastic and experienced Head Coach to lead and develop our training<br>programs, ensuring high-quality coaching for both junior and adult players whilst promoting the<br>club ethos and values throughout the club.<br><br><strong>Key Responsibilities:</strong><br><br><strong>Juniors Coaching:</strong><br>Design and deliver a structured training program for junior players (ages 8-16)<br>Work closely with a team of volunteer coaches to implement and enhance the program.<br>Organise matches for junior teams and appoint match managers.<br>Organise and deliver an annual summer camp for junior cricket.<br>Organise and deliver winter indoor training from early January (sessions will begin<br>indoors in January and transition to outdoor training during the spring/summer).<br>To occasionally attend meetings of the PPCC Cricket Working Group.<br>To be proactive in driving forward the development of the Clubs Junior Cricket<br>offering, engaging and communication with parents and Club officials.<br><br><strong>Adult Training:</strong><br>Plan and lead structured training sessions for adult players, primarily on Tuesday<br>evenings (6-8pm) during the summer.<br>Optional Coaching Opportunities:<br>Offer 1:1 coaching sessions for additional pay.<br>Utilise club facilities to expand coaching opportunities.</p>\n\n\n\n<p><strong>What We Offer:</strong><br>Competitive hourly rate.<br>Supportive and friendly club environment.<br>The opportunity to make a meaningful impact on the development of cricket at all levels<br>within our club.</p>\n\n\n\n<p><strong>Application Process:</strong><br>To apply, please submit your CV along with a brief summary of your previous coaching<br>experience and availability to Ian Pinnock, PPCC Director of Cricket at<br><a href="mailto:cricket@petworthparkcricket.org">cricket@petworthparkcricket.org</a></p>\n',
