    jobTitle: 'Coaching & Talent Specialist  Gold Coast',
    location: 'Location : Australia',
    address: 'Address : Carrara, Queensland, Australia',
    salary: 'Salary : Depending on experience',
    contractLength: 'Contract length : Full Time',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/01/1-20.png',
    email: null,
    website: null,
    contactNumber: null,
    description:
      '\n<p><strong>About the role</strong><br>Reporting to the Area Manager  Coach Development, in this role you will be responsible for the promotion, implementation and delivery of player and coach development across the Gold Coast region.</p>\n\n\n\n<p>Your key responsibilities will include:</p>\n\n\n\n<ul class="wp-block-list">\n<li>Engage with clubs to develop better training environments.</li>\n\n\n\n<li>Build, mentor and maintain a network of coaches (in both community and talent areas) across the region.</li>\n\n\n\n<li>Establish an annual calendar of coaching workshops and seminars.</li>\n\n\n\n<li>Lead, plan and deliver Community Coach Education courses.</li>\n\n\n\n<li>Support and guide the regions representative program.</li>\n\n\n\n<li>Support schools interested in developing cricket programs.</li>\n\n\n\n<li>Support and inform the delivery of engaging junior cricket formats.</li>\n</ul>\n\n\n\n<p><strong>About Queensland Cricket</strong><br>Queensland Cricket is responsible for leading and administering cricket throughout Queensland. Our purpose is to promote and grow cricket ensuring all Queenslanders can play, participate, and enjoy the game. Our flagship teams include Queensland Bulls, Queensland Fire and Brisbane Heat.</p>\n\n\n\n<p>Queensland Cricket values diversity and is an equal opportunity employer. Your age, gender, sexual orientation, disability or religion wont impact whether youre part of the team; its your drive to do great work thatll help you shine.</p>\n\n\n\n<p><strong>Keen to join our team?</strong><br>Click apply to submit your application, which includes a cover letter outlining the skills, experience and qualities that make you the best candidate.</p>\n\n\n\n<p>Well review applications as theyre received, so please apply early!</p>\n\n\n\n<p>Our values  We Care, We Lead, We Deliver, and We Innovate  are embedded in everything we do. Were eager to meet people who can contribute to our team in a variety of ways, not just candidates who check all the boxes. Even if you dont feel you meet every requirement for this role, we still encourage you to apply.</p>\n\n\n\n<p>We require all applicants to undergo background check and screening prior to or during any appointments. Queensland Cricket is committed to providing a safe environment for everyone involved in our sport, including children.</p>\n\n\n\n<p><em>Queensland Cricket acknowledges the lands of Yuggera people and the Turrbal people on which we live, work and play. We pay our respect to Aboriginal and Torres Strait Islander cultures; and to Elders past and present.&nbsp;</em></p>\n',
