    jobTitle: 'Casual Program Coordinator',
    location: 'Location : Australia',
    address: 'Address : New South Wales, Australia',
    salary: 'Salary : Depending on experience',
    contractLength: 'Contract length : Full Time',
    closingDate: null,
    logo: 'https://cricket.jobs/wp-content/uploads/2025/01/1-16.jpeg',
    email: null,
    website: null,
    contactNumber: null,
    description:
      '\n<p><strong>ABOUT US</strong>&nbsp;</p>\n\n\n\n<p>Cricket NSW&nbsp;(CNSW)&nbsp;is focused on leading and growing the game throughout the state. We oversee all cricket from elite professional teams to community, school, and social cricket programs. As a purpose and people led&nbsp;organisation, CNSW aims to inspire everyone to play and love cricket.&nbsp;</p>\n\n\n\n<p>Our Wildly Important&nbsp;Goal&nbsp;(WIG)&nbsp;is to&nbsp;grow the number of&nbsp;512-year-olds&nbsp;playing cricket.&nbsp;To deliver on our WIG and strategic priorities&nbsp;we&nbsp;have a&nbsp;collaborative, customer-centric&nbsp;approach.&nbsp;We use our values; Humility, Excellence, Imagination and Relationships to deliver&nbsp;memorable&nbsp;experiences to participants&nbsp;and fans.&nbsp;&nbsp;</p>\n\n\n\n<p><strong>ABOUT THE ROLE</strong>&nbsp;</p>\n\n\n\n<p>Casual&nbsp;Program Coordinators&nbsp;are responsible for&nbsp;facilitating&nbsp;the delivery of our junior cricket programs and experiences within schools,&nbsp;clubs&nbsp;and the wider community.&nbsp;You will play such&nbsp;an important&nbsp;role&nbsp;in growing cricket participation in&nbsp;the&nbsp;community.&nbsp;&nbsp;</p>\n\n\n\n<p>In this role you will:&nbsp;&nbsp;</p>\n\n\n\n<ul class="wp-block-list">\n<li>Coordinate&nbsp;and deliver&nbsp;engaging programs that teach important cricket skills to children in schools and clubs&nbsp;</li>\n\n\n\n<li>Promote community engagement throughout schools during the cricket season&nbsp;</li>\n\n\n\n<li>Provide in person quality delivery, engagement &amp; support of School programs including Health and PE, Sporting Schools, Mascot Challenge and School Cups&nbsp;&nbsp;</li>\n\n\n\n<li>Assist&nbsp;and expand participation in Club based entry level programs including&nbsp;CNSW Cricket Blast&nbsp;Centres&nbsp;</li>\n\n\n\n<li>Build,&nbsp;maintain&nbsp;and leverage relationships with local schools and school ambassadors&nbsp;</li>\n</ul>\n\n\n\n<p><strong>WHATS IN IT FOR YOU?</strong>&nbsp;</p>\n\n\n\n<p>We support and value our people in&nbsp;different ways, but here are a few benefits that are on offer for our people at Cricket NSW.&nbsp;</p>\n\n\n\n<ul class="wp-block-list">\n<li>Opportunity to work within the Sports industry for a large-scale state sporting&nbsp;organisation&nbsp;&nbsp;</li>\n</ul>\n\n\n\n<ul class="wp-block-list">\n<li>Youll&nbsp;be joining a team that is energetic and motivated, who deeply enjoy the work they do and the impact it has in the community&nbsp;</li>\n\n\n\n<li>A vibrant, purpose driven and professional culture that offers flexible working hours&nbsp;</li>\n\n\n\n<li>Youll&nbsp;spend majority of your shifts embracing the outdoors and sunshine,&nbsp;encouraging kids to play cricket&nbsp;</li>\n\n\n\n<li>Opportunities to develop and learn in the role through our comprehensive training and workshop offerings&nbsp;</li>\n\n\n\n<li>Access to exciting benefits with our corporate partners&nbsp;</li>\n\n\n\n<li>An&nbsp;organisation&nbsp;that has a focus on providing our people with strong leadership&nbsp;</li>\n</ul>\n\n\n\n<p><strong>KEEN TO JOIN OUR TEAM?</strong>&nbsp;</p>\n\n\n\n<p>Apply now! Submit a copy of your resume and cover letter.&nbsp;</p>\n\n\n\n<p>At Cricket NSW, we hire on merit and embrace diversity of characteristics, perspectives,&nbsp;skills&nbsp;and backgrounds. We are committed to&nbsp;providing&nbsp;a safe and inclusive environment for everyone, including children, so that people feel respected and connected, and can contribute and develop. We measure ourselves on our How We Play&nbsp;values.&nbsp;</p>\n\n\n\n<p>Australian Crickets Commitment Statement to Safeguarding Children and Young People can be found at cricketaustralia.com.au/safeguarding.&nbsp;</p>\n\n\n\n<p>We require all applicants to undergo background checks and screening prior to or during any appointments.&nbsp;</p>\n',
